export const none = 'none'
export const view = 'view'
export const edit = 'edit'

export const List = () => {
  return [
    none, view, edit
  ]
}

export const GetOrder = (q) => {
  switch (q) {
    case none:
      return 0;
    case view:
      return 1;
    case edit:
      return 2
  }
}