import * as util from './FieldUtil'

import * as inputUtil from '../../util/inputUtil'
import * as genUtil from '../../util/generalUtil'
import FieldView from './FieldView'

export class Field {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Key

  get Key_id() {
    return this.Key ? this.Key : this.id
  }

  // ref
  refData
  get RefData() {
    if (!this.refData) {
      this.refData = util.GetRefData(this)
    } return this.refData
  } set RefData(v) {
    this.refData = v;
  }

  // dict
  Dict

  // value
  valueGet
  get ValueGet() {
    if (!this.valueGet) {
      this.valueGet = util.GetDefault_ValueGet(this)
    } return this.valueGet
  } set ValueGet(v) {
    this.valueGet = v
  }

  valueSet
  get ValueSet() {
    if (!this.valueSet) {
      this.valueSet = util.GetDefault_ValueSet(this)
    } return this.valueSet
  } set ValueSet(v) {
    this.valueSet = v
  }

  ValueSet_OK

  value
  get Value() {
    return this.ValueGet();
  } set Value(v) {
    if (this.RefineValue) {
      if (!this.IsRefinedValue) {
        this.IsRefinedValue = true
        // v = this.RefineValue(v)
        this.ValueVM.Content = this.RefineValue(v);
        return;
      } else {
        this.IsRefinedValue = false
      }
    }

    this.ValueSet(v);

    if (this.OnSetValue) {
      this.OnSetValue(v)
    }
  }

  // value VM
  ValueVM_Render

  valueVM
  get ValueVM() {
    if (!this.valueVM) {
      var obj = this.valueVM =
        inputUtil.GetInput(this, 'Value', this.ValueSet_OK)
      obj.InputRegex = this.InputRegex

      if (this.ValueVM_Render) {
        this.ValueVM_Render(obj);
      }
    } return this.valueVM;
  }

  // Regex
  InputRegex

  // edit
  Ready = () => {
    if (this.PreReady) {
      if (!this.PreReady()) return;
    }

    inputUtil.Ready(this.ValueVM)
  }

  NotReady = () => {
    if (this.PreNotReady) {
      if (!this.PreNotReady()) return;
    }

    inputUtil.NotReady(this.ValueVM)
  }

  // edit
  get OnEdit() {
    return this.ValueVM.RenderState === 'edit'
  }

  Edit = () => {
    if (this.PreEdit) {
      if (!this.PreEdit()) return;
    }

    inputUtil.Edit(this.ValueVM)
  }

  EditOK = () => {
    if (this.PreEditOK) {
      if (!this.PreEditOK()) return;
    }

    inputUtil.EditOK(this.ValueVM)
  }

  // view
  viewRender
  get ViewRender() {
    if (!this.viewRender) {
      this.viewRender = util.GetDefault_ViewRender(this)
    } return this.viewRender
  } set ViewRender(v) {
    this.viewRender = v;
  }

  get View() {
    return this.ViewRender();
  }

  get WrapperView() {
    return util.Get_WrapperView(this)
  }
}