import { Dict, ExportSet_Dict } from "./Dict"
import * as iUtil from '../item/ItemUtil'

import * as genUtil from '../../../../../util/generalUtil'

// Instance
export const get = (dict) => {
  var qI = new Dict();

  qI.Dict = dict;

  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x => iUtil.GetFromDb(x, q))
    q.IsGet_Items = false;
  }

  const getApi = () => {
    var apiData = {
    }

    iUtil.GetData(q.Url, apiData, (data) => {
      setObj(data)
    })
  }

  getApi()
}


// Method
