import { url } from './Base_Url'
import { Base } from './Base'
import * as util from './Util'

import * as api from '../../../../../../util/apiUtil'
import * as genUtil from '../../../../../../util/generalUtil'

import { PMData } from '../../../../single/model/PMData'

const pmData = () => PMData.Instance

// Instance
export const GetFromData = (data, dict) => {
  var qI = get(dict)

  qI._id = data._id
  qI.CreatedDate = data.createdDate

  qI.coinId = data.coinId
  qI.Type = data.type
  qI.InitialPrice = data.initialprice
  qI.TakeProfit_Percent = data.takeprofitprecent
  qI.StopLoss_Percent = data.stoplosspercent

  qI.IsSubmit = data.issubmit
  qI.EarnPercent = data.earnpercent

  return qI
}

export const get = (dict) => {
  var qI = new Base()

  qI.Dict = dict

  return qI
}

export const Clone = (q, dict) => {
  var qI = get(dict)

  qI._id = q._id
  qI.Name = q.Name

  return qI
}

// Property
export const GetTakeProfit_Price = (q) => {
  return util.GetTakeProfit_Price(q.Type, q.InitialPrice, q.TakeProfit_Percent)
}

export const GetTakeProfit_Percent = (q) => {
  return util.GetTakeProfit_Percent(q.Type, q.InitialPrice, q.TakeProfit_Price)
}

export const GetStopLoss_Price = (q) => {
  return util.GetStopLoss_Price(q.Type, q.InitialPrice, q.StopLoss_Percent)
}

export const GetStopLoss_Percent = (q) => {
  return util.GetStopLoss_Percent(q.Type, q.InitialPrice, q.StopLoss_Price)
}

export const GetEarnPercent = (q) => {}

export const Handle_Coin = (q) => {
  if (q.IsGet_Coin) return
  q.IsGet_Coin = true

  const setObj = (data) => {
    q.Coin = data
    q.IsGet_Coin = false
  }

  var coinDict = pmData().CoinGroup.ItemDict
  coinDict.GetItem(q.coinId, (data) => setObj(data))
}

export const Handle_IsOwned = (q) => {
  if (q.IsGet_IsOwned) return
  q.IsGet_IsOwned = true

  const setObj = (data) => {
    q.IsOwned = data
    q.IsGet_IsOwned = false
  }

  var userDict = pmData().UserDict

  const func = () => {
    api
      .post('https://api.bimdev.vn/api/ba_addinvalidate/check', {
        addinId: q._id,
        userId: userDict.loginId,
      })
      .then((res) => res.data)
      .then((data) => setObj(data))
  }

  if (!userDict.loginId) {
    setObj(false)
  } else {
    func()
  }

  genUtil.Add_2(userDict.OnSet_loginId_Func, func, false)
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData)

  apiFunc()
    .then((response) => response.data)
    .then((data) => {
      if (handle) {
        handle(data)
      }
    })
}

export const GetOne = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/get`, apiData)

  apiFunc()
    .then((response) => response.data)
    .then((data) => {
      if (handle) {
        handle(data)
      }
    })
}
