import * as util from './GroupUtil'
import { Base as Coin } from '../../coin/base/base/Base'

import * as genUtil from '../../../../../util/generalUtil'

export class Group {
  Coin

  get InCoin() {
    return this.Dict instanceof Coin
  }

  futureDict
  get FutureDict() {
    if (!this.futureDict) {
      this.futureDict = util.GetFutureDict(this)
    }
    return this.futureDict
  }
}
