import axios from 'axios';
import { message } from 'antd';

export const API_URL = 'https://api.bimdev.vn/api';

// const http = axios.create({
//   baseURL: API_URL,
//   Accept: 'application/json',
//   'Content-Type': 'application/json',
//   //timeout: 20000
//   timeout: 20000000
// });

const http = (timeout) => axios.create({
  baseURL: API_URL,
  Accept: 'application/json',
  'Content-Type': 'application/json',
  timeout,
});

const handleNetworkError = (err, showMessage) => {
  if (showMessage) {
    message.error(`Error sending request: ${err}`);
  }
  return {
    data: undefined
  };
}

const handleResponse = (response) => {
  // Parse the axios response
  const success = response.status >= 200 && response.status < 300;
  const data = response.data;
  let message = response.statusText;
  return {
    success,
    data,
    message
  };
}

export const callApi = async (action, showMessage) => {
  try {
    const response = await action;
    return handleResponse(response);
  } catch (err) {
    console.log(err)
    return handleNetworkError(err, showMessage);
  } finally { }
}

export const timeout_default = 20000

export const get = async (url, config, timeout, showMessage = true) => {
  timeout = timeout ? timeout : timeout_default
  return callApi(http(timeout).get(url, config));
}

export const put = async (url, payload, config, timeout, showMessage = true) => {
  timeout = timeout ? timeout : timeout_default
  var result = await callApi(http(timeout).put(url, payload, config));
  return result;
}

export const post = async (url, payload, config, timeout, showMessage = true) => {
  timeout = timeout ? timeout : timeout_default
  var result = await callApi(http(timeout).post(url, payload, config));
  return result;
}

export const del = async (url, payload, timeout, showMessage = true) => {
  timeout = timeout ? timeout : timeout_default
  var result = await callApi(http(timeout).delete(url, payload));
  return result;
}