import { Dict } from "./Dict";
import * as genUtil from '../../../../../util/generalUtil'
// import * as targetUtil from '../target/TargetUtil'

// instance
export const get = (dict) => {
  var qI = new Dict();

  qI.Dict = dict;

  return qI;
}

// Method
export const GetTarget = (q, target) => {
  var list = q.Targets;
  var item = genUtil.SingleFilter(list, x => x.IsEqual(target));
  if (item == null) {
    // item = targetUtil.GetFromData(target, q);
    list.push(item);
  }
  return item;
}