import React, { Component } from 'react'
import { observer } from 'mobx-react'
import { Button, Switch } from 'antd'
import './Header.css'
import { Header } from './Header'
import { Link } from 'react-router-dom'
import { PMDataVM } from '../../../single/form/PMDataVM'
import { PMData } from '../../../single/model/PMData'
import * as vm from '../../view/ViewMode'

const pmDataVM = () => PMDataVM.Instance
const pmData = () => PMData.Instance

class HeaderView extends Component {
  constructor(props) {
    super(props)

    var { store } = this.props
    if (!store) {
      this.state = new Header()
    } else {
      this.state = store
    }
  }

  render() {
    var data = this.state
    var qI = pmDataVM()

    return (
      <>
        <div>
          {/* UserDict */}
          {/* {pmDataVM().UserDict.View} */}

          <div className="flex mt-2">
            <div className="ml-8">
              <Switch
                checkedChildren="view"
                unCheckedChildren="edit"
                checked={qI.ViewMode === vm.view}
                onChange={(v) => {
                  qI.ViewMode = v ? vm.view : vm.edit
                }}
              />
            </div>

            {/* <div className='ml-4 w-32'>
              {pmDataVM().UserDict.Demo.View}
            </div> */}
          </div>
        </div>
      </>
    )
  }
}

export default observer(HeaderView)
