import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import './Table.css'
import { DictVM } from '../dict/DictVM';

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new DictVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <table className='table table-bordered admin__um__table'>
          <thead className='admin__um__thead bg-[#e9ecef]'>
            <tr>
              <th className='admin__um__table__username'>Username</th>
              <th className='admin__um__table__password'>Password</th>
              <th className='admin__um__table__command'></th>
            </tr>
          </thead>
          <tbody className='admin__um__tbody'>
            {data.Items?.map(x => x.RowView)}
          </tbody>
        </table>
      </>
    );
  }
}

export default observer(TableView);
