import { Button, Popover } from 'antd'
import * as tt from './EntDelete_ToggleType'

// Property
export const GetDefault_Is_Visible = (q) => {
  return q.ToggleType === tt.manual ?
    true : false
}

export const GetDefault_Retrieve_ShowHover_Button = (q) => {
  return () => <Button type='primary' size='small'
    className={`btn_delete ${q.ClassName}`}
    style={q.Style}
    onClick={q.Show_Hover}
  >x</Button>
}

export const GetDefault_Run = (q) => {
  return () => {
    q.Is_Hover_Visible = false;
    if (q.OnRun) {
      q.OnRun();
    }
  }
}

export const GetDefault_Cancel = (q) => {
  return () => {
    q.Is_Hover_Visible = false;
    if (q.OnCancel) {
      q.OnCancel();
    }
  }
}

export const GetDefault_Retrieve_WrapperView = (q) => {
  return () => <Popover
    content={
      <>
        {q.Modal_ContentView}
      </>
    }
    title={q.Title}
    trigger="click"
    visible={q.Is_Hover_Visible}
    onVisibleChange={q.Toggle_Hover}
  >
    {q.ShowHover_Button}
  </Popover>
}

export const GetDefault_Modal_ButtonView = (q) => {
  return <div className='delete__btn-group'>
    <Button type='primary' size='small'
      onClick={q.PreRun}>ok</Button>
    <Button type='text' size='small'
      className='btn_blank'
      onClick={q.PreCancel}>cancel</Button>
  </div>
}

export const GetDefault_Modal_InfoView = (q) => {
  return ''
}

export const GetDefault_Modal_ContentView = (q) => {
  return <>
    {q.Modal_InfoView}
    {q.Modal_ButtonView}
  </>
}