import { User_DictVM } from "./User_DictVM";
import * as demoItemUtil from '../item/base/UserUtil'
import * as iUtil from '../item/base/UserVMUtil'
import * as loginUtil from '../login/LoginUtil'
import * as newpasswordUtil from '../newpassword/NewPasswordUtil'
import * as vt from './User_Dict_ViewType'
import './User_Dict.css'
import { Button, message } from 'antd';
import * as genUtil from '../../../util/generalUtil'
import * as adminUtil from '../admin/base/AdminVMUtil'
import * as userUtil from '../item/base/UserVMUtil'
import { SelectVM } from "../../select/SelectVM";

// Instance
export const get = (q, dict) => {
  var qI = new User_DictVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
