// login user
export const Handle_LoginUser = (q) => {
  if (q.IsGet_LoginUser) return undefined;
  q.IsGet_LoginUser = true;

  const setObj = (data) => {
    q.IsSet_Model = false
    q.LoginUser = data ? iUtil.get(data, q) : undefined
    q.IsGet_LoginUser = true;
  }

  var host = q.ModelItem;
  genUtil.Add_2(host.OnSet_loginId_Func,
    () => setObj(host.LoginUser))
  var q1 = host.LoginUser;
}

export const Handle_Users = (q) => {
  if (q.IsGet_Users) return undefined
  q.IsGet_Users = true;

  var url = q.Url

  const setObj = (data) => {
    q.Users = data.map(x => userUtil.get(x, q));
    q.IsGet_Users = false;
  }

  var host = q.ModelItem;
  if (host.Users) {
    setObj(host.Users)
  } else {
    genUtil.Add_2(host.OnSet_Users_Func,
      () => setObj(host.Users))
  }
}

export const GetAdmin = (q) => {
  var qI = adminUtil.get(q.ModelItem.Admin, q);
  return qI;
}

export const GetLogin = (q) => {
  return loginUtil.get(q);
}

export const GetNewPassword = (q) => {
  return newpasswordUtil.get(q)
}

export const Handle_ViewType = (q) => {
  if (q.IsGet_ViewType) return undefined;
  q.IsGet_ViewType = true;

  const setObj = () => {
    q.ViewType = GetViewType(q)
    q.IsGet_ViewType = false;
  }

  if (q.LoginUser) {
    setObj();
  } else {
    genUtil.Add_2(
      q.OnSet_LoginUser_Func,
      setObj
    )
  }
}

export const GetViewType = (q) => {
  return q.LoginUser ? vt.user : vt.login
}

export const GetDefault_LoginRender = (q) => {
  return () => q.Login.ButtonView;
}

export const GetDefault_UserRender = (q) => {
  return () => <>
    <div className='f-ac'>
      <div className='ud__username'
        onClick={q.NewPassword.OpenModal}>
        <b>{q.LoginUser.Username}</b>
      </div>
      <Button type='primary' size='small'
        className='btn-danger ud__logout'
        onClick={q.Logout}>
        x
      </Button>
    </div>
  </>
}

export const GetDefault_MainViewRender = (q) => {
  return () => {
    switch (q.ViewType) {
      case vt.login:
        return q.LoginRender()
      case vt.user:
        return q.UserRender();
      default:
        return ''
    }
    // q.ViewType === vt.login ?
    //   q.LoginRender() : q.UserRender();
  }
}

export const GetDefault_ModalViewRender = (q) => {
  return () => <>
    {q.Login.ModalView}
    {q.NewPassword.ModalView}
  </>
}

export const GetDefault_ViewRender = (q) => {
  return () => <div>
    <div className='ud__main__wrapper'>
      {q.MainView}
    </div>
    {q.ModalView}
  </div>
}