import { User_Dict } from "./User_Dict"
import * as api from '../../../util/apiUtil'
import * as userUtil from '../item/base/UserUtil'
import * as adminUtil from '../admin/base/AdminUtil'

import * as commandRender_DictUtil from '../item/command/render/dict/DictUtil'

// Instance
export const get = (url, dict) => {
  var qI = new User_Dict();
  qI.Url = url
  qI.Dict = dict;
  return qI;
}

// Property
export const Handle_LoginUser = (q) => {
  if (q.IsGet_LoginUser) return undefined;
  q.IsGet_LoginUser = true;

  const setObj = (data) => {
    q.LoginUser = data ? userUtil.GetFromDb(data, q) : undefined;
    q.IsGet_LoginUser = true;
  }

  if (q.Token) {
    const apiFunc = () => api.post(`${q.Url}/token`, {}, q.Config);
    apiFunc().then(response => response.data)
      .then(data => {
        setObj(data)
      })
  } else {
    setObj();
  }
}

export const Handle_Users = (q) => {
  if (q.IsGet_Users) return undefined
  q.IsGet_Users = true;

  var url = q.Url

  const setObj = (data) => {
    q.Users = data.map(x => userUtil.GetFromDb(x, q));
    q.IsGet_Users = false;
  }

  var apiData = {}

  userUtil.GetData(
    url,
    apiData, (data) => setObj(data)
  )
}

export const GetUser = (q, userId, handle) => {
  const setObj = (data) => {
    var qI = data.length !== 0 ? userUtil.GetFromDb(data[0]) : undefined
    handle(qI)
  }

  userUtil.GetData(q.Url, { userId }, (v) => setObj(v))
}

// admin
export const GetAdmin = (q) => {
  return adminUtil.get(q);
}

// Command Render
export const GetCommandRender_Dict = (q) => {
  return commandRender_DictUtil.get(q);
}