import { SummaryVM } from './SummaryVM'

import * as inputUtil from '../../../../../../util/inputUtil'
import * as genUtil from '../../../../../../util/generalUtil'

import { PMDataVM } from '../../../../single/form/PMDataVM'

const pmDataVM = () => PMDataVM.Instance

// Instance
export const get = (dict) => {
  var qI = new SummaryVM()

  qI.Dict = dict

  return qI
}

// Property
export const GetEarnPercent = (q) => {
  var list = q.Dict.Items

  if (!list) return undefined

  var qI = list
    .filter((x) => {
      return x.ModelItem.EarnPercent
    })
    .reduce((acc, a) => {
      return acc + a.ModelItem.EarnPercent
    }, 0)

  return qI
}

// view
export const GetRowView = (q) => {
  var earnPercent = q.EarnPercent

  return (
    <tr key="summary">
      <td />
      {!q.InCoin ? <td /> : undefined}
      <td
        colSpan={q.InCoin ? 2 : 2}
        className="non-input text-[#1890ff] font-semibold"
      >
        SUMMARY
      </td>
      <td></td>
      <td></td>
      <td></td>
      <td className="non-input">
        {earnPercent ? (
          <div
            className={`font-medium
            ${earnPercent > 0 ? 'text-[#1890ff]' : 'text-[#ff4d4f]'}`}
          >
            {earnPercent}
          </div>
        ) : (
          ''
        )}
      </td>
    </tr>
  )
}
