import { message } from 'antd';
import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../util/generalUtil'
import { TextVM } from '../../text/TextVM'
import * as util from './LoginUtil'

export class Login {
  constructor() {
    makeObservable(this, {
      Is_Modal_Visible: observable
    })
  }

  Username = ''

  usernameVM
  get UsernameVM(){
    if (!this.usernameVM){
      this.usernameVM = util.GetUsernameVM(this)
    } return this.usernameVM;
  }

  Password

  passwordVM
  get PasswordVM(){
    if (!this.passwordVM){
      this.passwordVM = util.GetPasswordVM(this)
    } return this.passwordVM;
  }

  get ApiData(){
    return {
      username : this.Username,
      password: this.Password
    }
  }

  Is_Modal_Visible = false

  get ButtonView(){
    return util.GetButtonView(this)
  }

  get MainView(){
    return util.GetMainView(this)
  }
  
  get ModalView(){
    return util.GetModalView(this)
  }

  ModalOK = () => {
    var username = this.Username;
    var pw = this.Password;
    if (!username || username.length === 0){
      message.warn('Bn phi nhp UserName!');
      return;
    }
    if (!pw || pw.length === 0){
      message.warn('Bn phi nhp Password!');
      return;
    }

    this.Validate();
  }

  Initial = () => {
    this.UsernameVM.Content = ''
    this.PasswordVM.Content = ''
  }

  Validate = () => {
    util.Validate(this)
  }
}