import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Field } from './Field';

import * as refUtil from '../../util/refUtil'

class FieldView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new Field()
    } else {
      this.state = store;
    }
  }

  componentDidMount() {
    refUtil.Export_RefData(this.state, 'mount');
  }

  componentWillUnmount() {
    refUtil.Export_RefData(this.state, 'unmount');
  }


  render() {
    var data = this.state;

    return (
      <>
        <div ref={data.RefData.GetRef('Target')}>
          {data.ValueVM.View}
        </div>
      </>
    );
  }
}

export default observer(FieldView);
