import { message } from 'antd';
import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../util/generalUtil'
import { TextVM } from '../../text/TextVM'
import * as util from './NewPasswordUtil'

export class NewPassword {
  constructor() {
    makeObservable(this, {
      Is_Modal_Visible: observable
    })
  }

  OldPassword

  oldPasswordVM
  get OldPasswordVM(){
    if (!this.oldPasswordVM){
      this.oldPasswordVM = util.GetOldPasswordVM(this)
    } return this.oldPasswordVM;
  }

  NewPassword

  newPasswordVM
  get NewPasswordVM(){
    if (!this.newPasswordVM){
      this.newPasswordVM = util.GetNewPasswordVM(this)
    } return this.newPasswordVM;
  }

  ReNewPassword

  reNewPasswordVM
  get ReNewPasswordVM(){
    if (!this.reNewPasswordVM){
      this.reNewPasswordVM = util.GetReNewPasswordVM(this)
    } return this.reNewPasswordVM;
  }

  get ApiData(){
    var obj = {
      _id : this.User.ModelItem?._id,
      oldpassword: this.OldPassword,
      newpassword : this.NewPassword
    }
    return obj;
  }

  get User(){
    return this.Dict.LoginUser
  }

  Is_Modal_Visible = false

  get ButtonView(){
    return util.GetButtonView(this)
  }

  get MainView(){
    return util.GetMainView(this)
  }
  
  get ModalView(){
    return util.GetModalView(this)
  }

  ModalOK = () => {
    var opw = this.OldPassword;
    var npw = this.NewPassword
    var rnpw = this.ReNewPassword;
    if (!opw || opw.length === 0){
      message.warn('Bn phi nhp Password c!');
      return;
    }
    if (!npw || npw.length === 0){
      message.warn('Bn phi nhp Password mi!');
      return;
    }
    if (opw === npw){
      message.warn('Password mi b trng vi password c!');
      return;
    }
    if (rnpw !== npw){
      message.warn('Thng tin nhp li Password mi khng ng!');
      return;
    }

    this.Validate();
  }

  OpenModal = () => {
    this.Initial();
    this.Is_Modal_Visible = true
  }

  Initial = () => {
    this.OldPasswordVM.Content = ''
    this.NewPasswordVM.Content = ''
    this.ReNewPasswordVM.Content = ''
  }

  Validate = () => {
    util.Validate(this)
  }
}