import { ItemVM } from "./ItemVM"
import * as inputUtil from '../../../../../util/inputUtil'
import { Button } from "antd";
import './Item.css'
import { EntDelete } from "../../../../delete/EntDelete";

// Instance
export const get = (q, dict) => {
  var qI = new ItemVM();

  qI.User = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetRowView = (q) => {
  return <tr key={q.User.id}>
    <td
      onMouseEnter={
        () => inputUtil.Edit(q.UsernameVM)}
      onMouseLeave={
        () => inputUtil.EditOK(q.UsernameVM)}
    >{q.UsernameView}</td>

    <td
      onMouseEnter={
        () => inputUtil.Edit(q.PasswordVM)}
      onMouseLeave={
        () => inputUtil.EditOK(q.PasswordVM)}
    >{q.PasswordView}</td>

    <td>
      <div className='f-jc'>
        <div className='f'>
          {q.IsPasswordChanged ?
            <Button type='primary' size='small'
              onClick={q.OK}>ok</Button>
            : ''}
          {q.IsUsernameChanged ?
            <Button type='primary' className='btn-warning' size='small'
              onClick={q.Cancel}>cancel</Button>
            : ''}

          {q.DeleteVM.View}
        </div>
      </div>

    </td>
  </tr >
}

export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = 'manual'
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}