import * as util from './DictUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import * as itemUtil from '../item/ItemUtil'

export class Dict {
  Items = []

  GetItem = (type) => {
    var list = this.Items;
    var item = genUtil.SingleFilter(list, x => x.Type === type);
    if (!item){
      item = itemUtil.GetFromData(type, this);
      list.push(item)
    } 
    return item;
  }
}