import { makeObservable, observable, action } from 'mobx';
import * as util from './User_DictUtil'
import * as genUtil from '../../../util/generalUtil'
import * as userUtil from '../item/base/UserUtil'

const userToken = 'userToken'

export class User_Dict {
  constructor() {
    makeObservable(this, {
      _loginId: observable,
    })
  }

  Url

  token
  get Token(){
    if (!this.token){
      this.token = localStorage.getItem(userToken);
    } return this.token
  } set Token(v){
    this.token = v
    if (!v){
      localStorage.removeItem(userToken)
    } else {
      localStorage.setItem(userToken,v)
    }
  }
  
  // LoginUser
  OnSet_loginId_Func =[]

  _loginId = undefined
  get loginId (){
    return this._loginId
  } set loginId(v){
    this._loginId = v

    genUtil.Handle(this.OnSet_loginId_Func,
      (v) => this.OnSet_loginId_Func = v)
  }

  loginUser
  get LoginUser(){
    if (!this.loginUser){
      util.Handle_LoginUser(this)
    } return this.loginUser;
  } set LoginUser(v){
    this.loginUser = v;
    this.loginId = v?._id

    if (!v){
      this.Token = v;
    }

    if (this.OnSet_LoginUser){
      this.OnSet_LoginUser(v);
    }
  }

  // Users
  OnSet_Users_Func = []

  users
  get Users(){
    if (!this.users){
      util.Handle_Users(this)
    } return this.users
  } set Users(v){
    this.users = v;

    genUtil.Handle(this.OnSet_Users_Func,
      (v) => this.OnSet_Users_Func = v)
  }

  AddUser = () => {
    var qI = userUtil.get(this);

    this.Users.push(qI);
    return qI;
  }

  GetUser = (userId, handle) => {
    util.GetUser(this, userId, handle)
  }

  // Admin
  admin
  get Admin(){
    if(!this.admin){
      this.admin = util.GetAdmin(this)
    } return this.admin;
  }

  // Command Render
  commandRender_Dict
  get CommandRender_Dict(){
    if (!this.commandRender_Dict){
      this.commandRender_Dict = util.GetCommandRender_Dict(this)
    } return this.commandRender_Dict;
  }

  get Config(){
    return {
      headers : {
        'Authorization': 'Bearer ' + this.Token
      }
    }
  }
}