import { TextVM } from "../../text/TextVM";
import * as ts from '../../text/TextState'
import { NewPassword } from "./NewPassword"
import InputView from "../../input/InputView";
import Modal from "antd/lib/modal/Modal";
import { Button, message } from 'antd';
import './NewPassword.css'
import * as api from '../../../util/apiUtil'

// Instance
export const get = (dict) => {
  var qI = new NewPassword();
  qI.Dict = dict;
  return qI;
}

// Property
export const GetOldPasswordVM = (q) => {
  var qI = new TextVM();

  qI.State = ts.password
  qI.Content = q.OldPassword;
  qI.onSetContent = (v) => q.OldPassword = v;

  return qI;
}

export const GetNewPasswordVM = (q) => {
  var qI = new TextVM();

  qI.State = ts.password
  qI.Content = q.NewPassword;
  qI.onSetContent = (v) => q.NewPassword = v;

  return qI;
}

export const GetReNewPasswordVM = (q) => {
  var qI = new TextVM();

  qI.State = ts.password
  qI.Content = q.ReNewPassword;
  qI.onSetContent = (v) => q.ReNewPassword = v;

  return qI;
}

// View
export const GetButtonView = (q) => {
  var click = () => {
    q.OpenModal();
  }

  return <Button type='primary' size='small'
    onClick={click}>
    New Password
  </Button>
}

export const GetMainView = (q) => {
  return <table className='newpassword__table'>
    <tbody>
      <tr className='newpassword__tr name'>
        <th className='newpassword__title'>
          Username:
        </th>
        <td className='newpassword__input'>
          <b>
            {q.User?.Username}
          </b>
        </td>
      </tr>
      <tr>
        <th className='newpassword__title'>
          OldPassword:
        </th>
        <td className='newpassword__input'>
          <InputView store={q.OldPasswordVM} />
        </td>
      </tr>
      <tr>
        <th className='newpassword__title'>
          NewPassword:
        </th>
        <td className='newpassword__input'>
          <InputView store={q.NewPasswordVM} />
        </td>
      </tr>
      <tr>
        <th className='login__title'>
          Re-NewPassword:
        </th>
        <td className='login__input'>
          <InputView store={q.ReNewPasswordVM} />
        </td>
      </tr>
    </tbody>
  </table>
}

export const GetModalView = (q) => {
  const hideVisible = () => {
    q.Is_Modal_Visible = false
  }

  return <Modal title="i mt khu" visible={q.Is_Modal_Visible}
    onCancel={hideVisible}
    width={400}
    footer={
      <>
        <Button type='primary' size='small'
          onClick={q.ModalOK}>ok</Button>
        <Button type='text' size='small'
          onClick={hideVisible}>cancel</Button>
      </>
    }>
    <div>
      {q.MainView}
    </div>
  </Modal>
}

// Method
export const Validate = (q) => {
  var dictVM = q.Dict;
  var dict = dictVM.ModelItem;

  const setObj = (data) => {
    if (data === 'wrongpassword'){
      message.warn('Password c khng ng!');
    }else if (data.length !== 0) {
      q.Is_Modal_Visible = false;
      message.success('change password successfully!');
      q.Dict.LoginUser = undefined
    }
  }

  const apiFunc = () => api.put(`${dictVM.Url}/newpassword`, q.ApiData);
  apiFunc().then(response => response.data)
    .then(data => {
      setObj(data)
    })
}