import { makeObservable, observable, action } from 'mobx';
import QuillToolView from './tool/QuillToolView';
import * as util from './QuillEditorUtil'
import * as genUtil from '../../util/generalUtil'

export class QuillEditor {
  constructor() {
    makeObservable(this, {
      content: observable,
      tools: observable,
      subTools: observable
    })
  }

  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = `q-${genUtil.randomId()}`;
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  content = ''
  get Content() {
    return this.content;
  }
  set Content(v) {
    this.content = v;
    if (this.OnSetContent) {
      this.OnSetContent(v);
    }
  }

  set InitialContent(v) {
    this.content = v;
  }

  placeholder
  get PlaceHolder() {
    if (!this.placeholder) {
      this.placeholder = 'vn bn son tho ...';
    }
    return this.placeholder;
  }
  set PlaceHolder(v) {
    this.placeholder = v;
  }

  Focus = () => {
    if (this.Ref) {
      this.Ref.current.focus();
    } else {
      this.IsFocusInit = true
    }
  }

  // Event
  OnAltEnter
  OnEmptyBackspace
  OnTab

  // Tools
  IsHaveTool = true

  tools = undefined
  get Tools() {
    if (!this.tools) {
      this.tools = util.GetDefault_Tools(this)
    }
    return this.tools
  } set Tools(v) {
    this.tools = v;
  }

  subTools = undefined
  get SubTools() {
    if (!this.subTools) {
      this.subTools = util.GetDefault_SubTools(this)
    } return this.subTools
  } set SubTools(v) {
    this.subTools = v;
  }

  get FullTools() {
    return this.Tools.concat(this.SubTools);
  }

  GetTool = (type) => {
    var index = this.FullTools.findIndex(x => x.Type === type);
    return index !== -1 ? this.FullTools[index] : undefined
  }

  get Tool_Container() {
    return <div id={this.id} className='ql-snow wrapper'>
      <div className='ql__tool__main'>
        {this.Tools.map(x => {
          return <QuillToolView key={x.Type} store={x} />
        })}
      </div>
      <div className='ql__tool__sub'>
        {this.SubTools.map(x => {
          return <QuillToolView key={x.Type} store={x} />
        })}
      </div>
    </div>
  }

  get Handles() {
    var obj = {}
    this.Tools.filter(x => x.Handle !== undefined).forEach(x => {
      obj[x.Type] = x.Handle
    })

    return obj;
  }
}