import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import { DictVM } from '../dict/DictVM';
import './Table.css'
import * as commandType from '../../command/base/CommandType'

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new DictVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <table className='table table-bordered'>
          <thead className='mt__thead'>
            <tr>
              <th className='role__table__name'/>

              {commandType.List().map(x => {
                return <td key={x} className='role__table__item'>
                  {commandType.GetName(x)}
                </td>
              })}

              <th className='role__table__command'></th>
            </tr>
          </thead>
          <tbody>
            {data.Items?.map(x => x.RowView)}
          </tbody>
        </table>
      </>
    );
  }
}

export default observer(TableView);
