import { makeObservable, observable, action } from 'mobx'
import { Button, message } from 'antd'
import * as util from './BaseVMUtil'

import * as genUtil from '../../../../../../../util/generalUtil'

export class BaseVM {
  constructor() {
    makeObservable(this, {})
  }

  get id() {
    return this.ModelItem.id
  }

  // data
  fields
  get Fields() {
    if (!this.fields) {
      this.fields = util.GetFields(this)
    }
    return this.fields
  }

  GetField = (name) => {
    return genUtil.SingleFilter(this.Fields, (x) => x.Key === name)
  }

  // star review
  get Star() {
    return this.ModelItem.Star
  }

  get StarView() {
    return util.GetStarView(this)
  }

  get IsOwner() {
    return this.Dict.IsOwner
  }

  // save
  Save = (handle) => {
    this.ModelItem.Save(handle)
  }

  // validate
  Validate = (level) => {
    return this.Dict.Validate(level)
  }

  // view
  get View() {
    return util.GetView(this)
  }
}
