import * as cl from './CommandLevel'
import * as util from './CommandUtil'

export class Command {
  get id() {
    return this.Type
  }

  Type

  Level = cl.none

  // apidata
  get ApiData(){
    return {
      type : this.Type,
      level : this.Level
    }
  }

  // validate
  Validate = (data) => {
    return util.Validate(this, data)
  }
}