import { Group } from './Group'
import * as futureDictUtil from '../future/dict/DictUtil'
import { PMData } from '../../../single/model/PMData'

const pmData = () => PMData.Instance

// Instance
export const get = (dict) => {
  var qI = new Group()

  qI.Dict = dict

  return qI
}

// Property
export const GetFutureDict = (q) => {
  return futureDictUtil.get(q)
}
