import { makeObservable, observable, action } from 'mobx'
import { Button, message } from 'antd'
import * as util from './SummaryVMUtil'
import * as f from '../field/Field'

import * as genUtil from '../../../../../../util/generalUtil'

export class SummaryVM {
  constructor() {
    makeObservable(this, {})
  }

  get EarnPercent() {
    return util.GetEarnPercent(this)
  }

  get InCoin() {
    return this.Dict.InCoin
  }

  // view
  get RowView() {
    return util.GetRowView(this)
  }
}
