import { Component } from "react";
import React from 'react'
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Input } from "antd";
import * as imageUtil from './ImageUtil'

class ImageView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = imageUtil.GetTest();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;
    var style = this.props.style ?
      this.props.style : data.Style;

    var className = this.props.className ?
      this.props.className : data.ClassName

    var onClick = this.props.onClick;
    var onDoubleClick = this.props.onDoubleClick;
    var onMouseEnter = this.props.onMouseEnter;
    var onMouseLeave = this.props.onMouseLeave;

    return (
      <>
        <img className={className} style={style}
          src={data.Source} alt={data.Alt}
          width={data.Width} height={data.Height} 
          onClick={onClick} 
          onDoubleClick={onDoubleClick}
          onMouseEnter={onMouseEnter}
          onMouseLeave={onMouseLeave}/>
      </>
    )
  }
}

export default observer(ImageView)