import { Admin } from "./Admin";
import * as userUtil from '../../item/base/UserUtil'
import * as auDictUtil from '../adminuser/dict/DictUtil'

// Instance
export const get = (dict) => {
  var qI = new Admin();

  qI.Dict = dict;

  return qI;
}

// Property
export const Handle_Users = (q) => {
  if (q.IsGet_Users) return undefined
  q.IsGet_Users = true;

  var url = q.Url

  const setObj = (data) => {
    q.Users = data.map(x => {
      return q.GetUser_Func(x)
    });
    q.IsGet_Users = false;
  }

  var apiData = {}

  userUtil.GetData(
    url,
    apiData, (data) => setObj(data)
  )
}

// dict
export const GetAdminUser_Dict = (q) => {
  return auDictUtil.get(q)
}

// Method
