import { Group } from './Group'
import * as itemDictUtil from '../base/dict/DictUtil'
import * as baseUtil from '../base/base/BaseUtil'
import { PMData } from '../../../single/model/PMData'

const pmData = () => PMData.Instance

// Instance
export const get = (dict) => {
  var qI = new Group()

  qI.Dict = dict

  return qI
}

// Property
export const GetItemDict = (q) => {
  return itemDictUtil.get(q)
}

export const Handle_CurrentItem = (q) => {
  var routeData = pmData().RouteData
  if (routeData[0] === 'home') return

  if (q.IsGet_CurrentItem) return
  q.IsGet_CurrentItem = true

  const setObj = (data) => {
    q.CurrentItem = baseUtil.GetFromData(data, q)
    q.IsGet_CurrentItem = false
  }

  var apiData = {
    _id: routeData[0].meta,
  }

  baseUtil.GetOne(apiData, (data) => setObj(data))
}
