import * as genUtil from '../../../../../../../util/generalUtil'
import { PMData } from '../../../../../single/model/PMData'
import * as targetUtil from '../_target/TargetUtil'

import {Dict} from './Dict'

const pmData = () => PMData.Instance;

// instance
export const get = (dict) => {
  var qI = new Dict();

  genUtil.Add_2(pmData().UserDict.OnSet_LoginUser_Func,
    () => qI.Targets.forEach(x => {
      x.Refresh_Role();
      // x.Role = undefined
    }), false)

  qI.Dict = dict;
  return qI;
}

// property
export const GetUser = (q) => {
  return pmData().UserDict.LoginUser
}

// method
export const GetTarget = (q, data) => {
  var target = targetUtil.GetFromData(data, q);
  var list = q.Targets

  var item = genUtil.SingleFilter(list, x => x.IsEqual(target));
  if (!item){
    list.push(target);
    return target;
  }
  return item;
}