import React, { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button } from "antd";
import { EditableTextVM } from "./EditableTextVM";
import './Text.css'

class EditableTextView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new EditableTextVM();
    } else {
      this.state = store;
    }

    var data = this.state;
    data.Ref = React.createRef();
  }

  render() {
    var data = this.state;
    data.ClassName = this.props.className ? this.props.className : ''
    data.Style = this.props.style

    data.OnFocus = data.OnFocus ?
      data.OnFocus : this.props.onFocus

    data.OnDoubleClick = data.OnDoubleClick ?
      data.OnDoubleClick : this.props.onDoubleClick

    data.OnClick = data.OnClick ?
      data.OnClick : this.props.onClick

    return (
      <>
        {data.Render()}
      </>
    )
  }
}

export default observer(EditableTextView)