export const code = 'code'
export const name = 'name'
export const ref_rationame = 'ref_rationame'

export const List = () => {
  return [code, name, ref_rationame]
}

export const GetName = (param) => {
  var r_name

  switch (param) {
    case code:
      r_name = 'Code'
      break
    case name:
      r_name = 'Name'
      break
    case ref_rationame:
      r_name = 'Reference_RatioName'
      break
  }

  return r_name
}

export const GetTitle = (param) => {
  var r_name

  switch (param) {
    case code:
      r_name = 'Code'
      break
    case name:
      r_name = 'Name'
      break
    case ref_rationame:
      r_name = 'Reference RatioName'
      break
  }

  return r_name
}

export const GetPlaceholder = (param) => {
  var r_name

  switch (param) {
    case code:
      r_name = 'code'
      break
    case name:
      r_name = 'name'
      break
    case ref_rationame:
      r_name = 'ref ratio'
      break
  }

  return r_name
}
