import { makeObservable, observable, action } from 'mobx';
import { message } from 'antd';
import * as util from './CommandVMUtil'
import * as commandLevel from './CommandLevel'

export class CommandVM {
  constructor() {
    makeObservable(this, {
      level : observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }
  
  get Type(){
    return this.ModelItem.Type
  }

  // level
  level = undefined
  get Level(){
    if (!this.level){
      this.level = this.ModelItem.Level
    } return this.level
  } set Level(v){
    this.level = this.ModelItem.Level = v
  }

  UpLevel = () => {
    util.UpLevel(this)

    // save
    this.Dict.Save();
  }

  // View
  get View(){
    return util.GetView(this)
  }
}