import { AdminVM } from "./AdminVM";
import * as genUtil from '../../../../util/generalUtil'
import * as um_dUtil from '../usermanager/dict/DictVMUtil'
import * as auDictUtil from '../adminuser/dict/DictVMUtil'

// Instance
export const get = (q, dict) => {
  var qI = new AdminVM();

  qI.ModelItem = q;
  qI.Dict = dict

  return qI;
}

// Property
export const GetDefault_ViewRender = (q) => {
  return () => q.UserManager_Dict.MainView
}

export const GetUserManager_Dict = (q) => {
  return um_dUtil.get(undefined, q);
}

export const GetAdminUser_Dict = (q) => {
  return auDictUtil.get(q.ModelItem.AdminUser_Dict, q);
}