import { GroupVM } from './GroupVM'
import * as futureDictUtil from '../future/dict/DictVMUtil'

import * as genUtil from '../../../../../util/generalUtil'

// Instance
export const get = (q, dict) => {
  var qI = new GroupVM()
  qI.ModelItem = q
  qI.Dict = dict
  return qI
}

// Property
export const GetFutureDict = (q) => {
  return futureDictUtil.get(q.ModelItem.FutureDict, q)
}

// view
export const GetView = (q) => {
  return (
    <div>
      {/* Future */}
      <div>
        <div className="text-lg font-medium">Future</div>

        <div className="mt-2">{q.FutureDict.View}</div>
      </div>
    </div>
  )
}
