import { Component } from "react";
import React from 'react'
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Input } from "antd";
import * as textVMUtil from '../text/TextVMUtil'
import ContentEditable from "react-contenteditable";
import * as ts from '../text/TextState'
import * as genUtil from '../../util/generalUtil'

class NewInputView extends Component {
  constructor(props) {
    super(props);

    this.contentEditable = React.createRef();
    var { store } = this.props;
    if (!store) {
      var content = `Ni dung input`
      this.state = textVMUtil.get(content);
    } else {
      this.state = store;
    }

    var data = this.state;
    data.Ref = React.createRef();
  }

  handleChange = (e) => {
    var data = this.state;
    data.Content = e.target.value;
  };

  OnPaste = (e) => {
    e.preventDefault();
    var text = genUtil.GetPlainText(e);

    var data = this.state;
    data.Content = text;
  }

  OnKeyDown = (e) => {
    var data = this.state;
    if (data.OnKeyDown){
      data.OnKeyDown(e)
      return;
    }

    // Ctrl + B
    if (e.ctrlKey && e.which == 66) {
      e.preventDefault();
    }
    // Ctrl + I
    else if (e.ctrlKey && e.which == 73) {
      e.preventDefault();
    }
    // Enter
    else if (e.keyCode == 13) {
      e.preventDefault();
    }
  }

  render() {
    var data = this.state;
    var Content = data.Content;
    var className = this.props.className ? this.props.className : ''
    var style = this.props.style
    var onFocus = this.props.onFocus ? 
      this.props.onFocus : data.OnFocus

    return (
      <>
        {data.State === ts.text ?
          <ContentEditable ref={data.Ref}
            className={`ant-input ${className}`}
            style={style}
            innerRef={this.contentEditable}
            placeholder={data.Placeholder}
            style={style}
            html={Content ? Content : ''}
            onChange={this.handleChange}
            onPaste={this.OnPaste}
            onKeyDown={this.OnKeyDown} 
            onFocus={onFocus}/>
          : ''}
        {data.State === ts.password ?
          <Input.Password value={Content} onChange={this.handleChange}
            className={className}
            style={style} /> : ''}
      </>
    )
  }
}

export default observer(NewInputView)