import { TextVM } from "../../text/TextVM";
import * as ts from '../../text/TextState'
import { Login } from "./Login"
import InputView from "../../input/InputView";
import Modal from "antd/lib/modal/Modal";
import { Button, message } from 'antd';
import './Login.css'
import * as uUtil from '../item/base/UserUtil'
import * as uVMUtil from '../item/base/UserVMUtil'

// Instance
export const get = (dict) => {
  var qI = new Login();
  qI.Dict = dict;
  return qI;
}

// Property
export const GetUsernameVM = (q) => {
  var qI = new TextVM();

  qI.Placeholder = 'tn user'
  qI.Content = q.Username;
  qI.onSetContent = (v) => q.Username = v;

  return qI;
}

export const GetPasswordVM = (q) => {
  var qI = new TextVM();

  qI.State = ts.password
  qI.Content = q.Password;
  qI.onSetContent = (v) => q.Password = v;

  return qI;
}

// View
export const GetButtonView = (q) => {
  var click = () => {
    q.Initial();
    q.Is_Modal_Visible = true
  }

  return <Button type='primary' size='small'
    onClick={click}>
    Login
  </Button>
}

export const GetMainView = (q) => {
  return <table className='login__table'>
    <tbody>
      <tr className='login__tr name'>
        <th className='login__title'>
          Username:
        </th>
        <td className='login__input'>
          <InputView store={q.UsernameVM} />
        </td>
      </tr>
      <tr>
        <th className='login__title'>
          Password:
        </th>
        <td className='login__input'>
          <InputView store={q.PasswordVM} />
        </td>
      </tr>
    </tbody>
  </table>
}

export const GetModalView = (q) => {
  const hideVisible = () => {
    q.Is_Modal_Visible = false
  }

  return <Modal title="ng nhp" visible={q.Is_Modal_Visible}
    onCancel={hideVisible}
    width={350}
    footer={
      <>
        <Button type='primary' size='small'
          onClick={q.ModalOK}>ok</Button>
        <Button type='text' size='small'
          onClick={hideVisible}>cancel</Button>
      </>
    }>
    <div>
      {q.MainView}
    </div>
  </Modal>
}

// Method
export const Validate = (q) => {
  var dictVM = q.Dict;
  var dict = dictVM.ModelItem;

  const setObj = (data) => {
    if (data.length !== 0) {
      dictVM.LoginUser = uVMUtil.get(uUtil.GetFromDb(data[0], dict), dictVM);
      q.Is_Modal_Visible = false;
    } else {
      message.warn('Thng tin ng nhp khng ng!')
    }
  }

  uUtil.GetData(dictVM.Url, q.ApiData, (data) => {
    setObj(data)
  });
}