import { message } from 'antd';
import * as genUtil from '../../../../util/generalUtil'
import * as util from './UserUtil'

export class User {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  userName
  get Username(){
    if (!this.userName){
      this.userName = 'username'
    } return this.userName
  } set Username(v){
    this.userName = v
  }

  Password

  Name

  get ApiData() {
    var obj = {
      _id: this._id,
      username : this.Username,
      name : this.Name,
      password : this.Password
    }

    return obj;
  }


  get Url() {
    return this.Dict.Url
  }

  Save = (handle) => {
    this.SaveDatabase(false, handle);
    this.Password = undefined
  } 

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, this.Url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }

  // Index
  get CurrentList() {
    return this.Dict.Users
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)

    if (this._id) {
      this.DeleteDatabase(false)
    }
  }

  DeleteDatabase = (showMessage = true, handle) => {
    genUtil.DeleteDatabase(this, this.Url, () => {
      if (showMessage) {
        message.success('delete successflly!');
      }
      if (handle) {
        handle()
      }
    });
  }

  // command
  commandDict
  get CommandDict(){
    if (!this.commandDict){
      this.commandDict = util.GetCommandDict(this)
    } return this.commandDict;
  }
}