export const type = 'type'
export const initialprice = 'initialprice'

export const takeprofitprice = 'takeprofitprice'
export const takeprofitprecent = 'takeprofitprecent'

export const stoplossprice = 'stoplossprice'
export const stoplosspercent = 'stoplosspercent'

export const List = () => {
  return [
    initialprice, //
    takeprofitprice,
    takeprofitprecent,
    stoplossprice,
    stoplosspercent,
  ]
}

export const GetName = (param) => {
  var r_name

  switch (param) {
    case initialprice:
      r_name = 'InitialPrice'
      break
    case takeprofitprice:
      r_name = 'TakeProfit_Price'
      break
    case takeprofitprecent:
      r_name = 'TakeProfit_Percent'
      break
    case stoplossprice:
      r_name = 'StopLoss_Price'
      break
    case stoplosspercent:
      r_name = 'StopLoss_Percent'
      break
  }

  return r_name
}

export const GetTitle = (param) => {
  var r_name

  switch (param) {
    case initialprice:
      r_name = 'InitialPrice'
      break
  }

  return r_name
}

export const GetPlaceholder = (param) => {
  var r_name

  switch (param) {
    case initialprice:
      r_name = 'init price'
      break
  }

  return r_name
}
