import * as editTextUtil from '../model/text/EditableTextVMUtil'
import * as genUtil from './generalUtil'

export const alt = 'alt'
export const alt_enter = 'alt_enter'

export const ctrl = 'ctrl'
export const ctrl_b = 'ctrl_b'
export const ctrl_i = 'ctrl_i'

export const enter = 'enter'
export const tab = 'tab'
export const backspace = 'backspace'
export const del = 'del'
export const esc = 'esc'

export const Get_Shortcut = (e) => {
  var key;
  if (e.altKey && e.keyCode == 13) {
    key = alt_enter
  } else if (e.ctrlKey && e.which == 66) {
    key = ctrl_b
  } else if (e.ctrlKey && e.which == 73) {
    key = ctrl_i
  } else if (e.ctrlKey) {
    key = ctrl
  } else if (e.altKey) {
    key = alt
  } else if (e.keyCode == 13) {
    key = enter
  } else if (e.keyCode == 9) {
    key = tab
  } else if (e.keyCode == 8) {
    key = backspace
  } else if (e.keyCode == 46) {
    key = del
  } else if (e.keyCode == 27) {
    key = esc
  }

  return {
    key,
    value: e.key
  }
}

// Edit RenderState
export const Ready = (q, preFunc, postFunc) => {
  var isValid = preFunc ? preFunc() : true;
  if (!isValid) return;

  if (q.RenderState !== 'edit') {
    q.RenderState = 'ready';
  }

  if (postFunc) {
    postFunc();
  }
}

export const NotReady = (q) => {
  if (q.RenderState !== 'edit') {
    q.RenderState = 'default'
  }
}

export const Edit = (q, preFunc, postFunc) => {
  var isValid = preFunc ? preFunc() : true;
  if (!isValid) return;

  if (q.RenderState === 'default') {
    q.RenderState = 'edit';

    if (postFunc) {
      postFunc();
    }
  }
}

export const EditOK = (q, postFunc) => {
  q.RenderState = 'default';

  if (postFunc) {
    postFunc();
  }
}

// Get InputView
export const GetInput = (q, field, postOkFunc) => {
  var obj = editTextUtil.get(q[field]);

  const set_default = () =>
    EditOK(obj);

  obj.Is_Tool_Visible = false
  obj.OnKeyDown = (e) => {
    var { key, value } = Get_Shortcut(e);

    switch (key) {
      case enter:
        if (!obj.StorageType || obj.StorageType === 'text') {
          set_default();
        }
        break;
      case alt_enter:
        if (obj.StorageType === 'html') {
          set_default();
        }
        break;
      case esc:
        obj.IsEscape = true;
        set_default();
        break;
    }
  }

  genUtil.Add(
    obj.OnSet_RenderState_Func_Dict,
    () => {
      if (obj.RenderState === 'default' && obj.Old_RenderState === 'edit') {
        const okfunc = () => {
          if (postOkFunc) {
            postOkFunc();
          }
        }

        if (obj.IsEscape) {
          obj.Editor.Content = obj.Content = q[field]
          obj.IsEscape = false

          okfunc();
          return;
        }

        if (obj.Content === q[field]) {
          okfunc();
          return;
        }

        q[field] = obj.Content;
        okfunc();
      }
    },
    false
  )

  return obj;
}

// Phng thc c ang s dng trong HtmlRenderVM
export const get = (inputs, name) => {
  var index = inputs.findIndex(x => x.name === name);
  if (index === -1) {
    inputs.push(getDefaultInput(name));
    index = inputs.length - 1;
  }
  return inputs[index];
}

export const getDefaultInput = (name) => {
  let value;
  switch (name) {
    case '0':
      value = 0;
      break;
    case 'x':
      value = 3;
      break;
    case 'class':
      value = 'Person';
      break;
    case 'classDesc':
      value = 'Kiu d liu m phng ...';
      break;
    default:
      value = 'undefined';
      break;
  }

  return { name, value }
}