import { makeObservable, observable, action } from 'mobx';
import * as util from './QuillToolUtil'

export class QuillTool {
  constructor() {
    makeObservable(this, {
      data: observable,
    })
  }

  Type

  get ToolClass(){
    return `ql-${this.Type}`
  }

  toolView
  get ToolView(){
    if (!this.toolView){
      this.toolView = util.Get_DefaultToolView(this)
    } return this.toolView
  } set ToolView(v){
    this.toolView = v;
  }

  contentView
  get ContentView(){
    if (!this.contentView){
      this.contentView = util.Get_DefaultContentView(this)
    } return this.contentView
  } set ContentView(v){
    this.contentView = v;
  }

  Handle

  data = undefined
  get Data(){
    if (!this.data) {
      this.data = util.Get_DefaultData(this)
    } return this.data
  } set Data(v){
    this.data = v;
  }
}