import { makeObservable, observable, action } from 'mobx';
import { ChromePicker } from 'react-color';

export class ColorPicker {
  constructor() {
    makeObservable(this, {
      color: observable,
    })
  }

  color = "#000000"
  get Color() {
    return this.color
  } set Color(v) {
    this.color = v;
    if (this.OnSetColor) {
      this.OnSetColor(v);
    }
  }

  PickColor = (c) => {
    this.Color = c.hex
  }

  get MainView() {
    return <ChromePicker color={this.Color}
      onChange={this.PickColor}
    />
  }
}