import { makeObservable, observable, action } from 'mobx'
import { message } from 'antd'
import { url } from './Base_Url'
import * as util from './BaseUtil'
import { PMData } from '../../../../single/model/PMData'

import * as genUtil from '../../../../../../util/generalUtil'

const pmData = () => PMData.Instance

export class Base {
  constructor() {
    makeObservable(this, {
      __id: observable,
      Name: observable,
      isOwned: observable,
    })
  }

  OnSet_id_Func_Dict = []
  __id = undefined
  get _id() {
    return this.__id
  }
  set _id(v) {
    this.__id = v
    genUtil.Handle(
      this.OnSet_id_Func_Dict,
      (v) => (this.OnSet_id_Func_Dict = v)
    )
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID()
    }
    return this.tempId
  }

  get id() {
    return this._id ? this._id : this.TempId
  }

  Code = 'code'

  Name = 'name'

  Reference_RatioName = 'ref ration name'

  // trade
  tradeGroup
  get TradeGroup() {
    if (!this.tradeGroup) {
      this.tradeGroup = util.GetTradeGroup(this)
    }
    return this.tradeGroup
  }

  // info
  OnGet_Info_Func = []

  info
  get Info() {
    if (!this.info) {
      util.Handle_Info(this)
    }
    return this.info
  }
  set Info(v) {
    this.info = v

    genUtil.Handle(this.OnGet_Info_Func, (v) => (this.OnGet_Info_Func = v))
  }

  // s hu
  isOwned = undefined
  get IsOwned() {
    if (this.isOwned === undefined) {
      util.Handle_IsOwned(this)
    }
    return this.isOwned
  }
  set IsOwned(v) {
    this.isOwned = v
  }

  //
  get IsOwner() {
    return this.creatorId === pmData().UserDict.loginId
  }

  // clone
  Clone = (dict) => {
    return util.Clone(this, dict)
  }

  // apidata
  get ApiData() {
    var obj = {
      _id: this._id,
      code: this.Code,
      name: this.Name,
      ref_rationame: this.Reference_RatioName,
    }

    return obj
  }

  // Save
  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url, () => {
      if (showMessage) {
        message.success('save successfully!')
      }
      if (handle) {
        handle()
      }
    })
  }

  Save = (handle) => {
    const func = () => {
      if (handle) {
        handle()
      }
    }

    const finish = () => {
      this.OnSave = false
      func()
    }

    if (this.OnSave) {
      func()
      return
    } else {
      this.OnSave = true
    }

    this.SaveDatabase(false, finish)
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.DeleteDatabase(false)
  }

  DeleteDatabase = (showMessage = true, handle) => {
    genUtil.DeleteDatabase(this, url, () => {
      if (showMessage) {
        message.success('delete successfully!')
      }
      if (handle) {
        handle()
      }
    })
  }
}
