import * as genUtil from '../../../../../util/generalUtil'
import { DictVM } from "./DictVM";
import './Dict.css'
import { Button } from 'antd';
import * as itemUtil from '../item/ItemVMUtil'
import * as editTextUtil from '../../../../text/EditableTextVMUtil';
import EditableTextView from '../../../../text/EditableTextView';
import * as rs from '../../../../text/TextRenderState';

// Instance
export const get = (q, dict) => {
  var qI = new DictVM();

  qI.ModelItem = q;
  qI.Dict = dict

  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x => itemUtil.get(x, q));
    q.IsGet_Items = false;
  }

  var host = q.Dict.Dict
  if (host.Users) {
    setObj(host.Users)
  }

  genUtil.Add_2(
    host.OnSet_Users_Func,
    () => setObj(host.Users),
    false
  )
}

