import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button } from 'antd';
import { DictVM } from '../dict/DictVM';

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new DictVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <table>
          <tbody>
            {data.Items?.map(x => x.RowView)}
          </tbody>
        </table>
      </>
    );
  }
}

export default observer(TableView);
