import { makeObservable, observable, action } from 'mobx';
import * as tt from './EntDelete_ToggleType'
import * as vt from './EntDelete_VisibleType'
import * as ut from './EntDelete_UseType'
import * as util from './EntDeleteUtil'
import EntDeleteView from './EntDeleteView';
import { TextVM } from '../text/TextVM';
import * as genUtil from '../../util/generalUtil'

export class EntDelete {
  constructor() {
    makeObservable(this, {
      is_Visible: observable,
      is_Hover_Visible: observable,
      VisibleType: observable,
      UseType: observable,
      ClassName: observable,
      Style: observable
    })
  }

  _id
  get id() {
    if (!this._id) {
      this._id = genUtil.newGUID();
    } return this._id;
  } set id(v) {
    this._id = v;
  }

  is_Visible = undefined
  get Is_Visible() {
    if (!this.is_Visible) {
      this.is_Visible = util.GetDefault_Is_Visible(this)
    } return this.is_Visible
  } set Is_Visible(v) {
    this.is_Visible = v;
  }

  ToggleType = tt.manual

  VisibleType = vt.collapse

  is_Hover_Visible = undefined
  get Is_Hover_Visible() {
    return this.is_Hover_Visible
  } set Is_Hover_Visible(v) {
    this.is_Hover_Visible = v;

    if (this.ToggleType === tt.mouseEvent) {
      if (!v) {
        this.Is_Visible = false
      }
    }

    if (this.OnSet_Is_Hover_Visible) {
      this.OnSet_Is_Hover_Visible(v);
    }
  }

  Show_Hover = (e) => {
    if (e) {
      e.stopPropagation();
    }

    if (this.OnPre_ShowHover) {
      this.OnPre_ShowHover();
    }

    this.Is_Hover_Visible = true;
  }

  Toggle_Hover = (v) => {
    this.Is_Hover_Visible = v
  }

  // Run
  IsRun_Cancel

  PreRun = (e) => {
    e.stopPropagation();

    this.IsRun_Cancel = false
    if (this.OnPreRun) {
      this.OnPreRun()
    }

    if (!this.IsRun_Cancel) {
      this.Run();
    }
  }

  run
  get Run() {
    if (!this.run) {
      this.run = util.GetDefault_Run(this)
    } return this.run
  } set Run(v) {
    this.run = v;
  }

  PreCancel = (e) => {
    e.stopPropagation();
    this.Cancel();
  }

  cancel
  get Cancel() {
    if (!this.cancel) {
      this.cancel = util.GetDefault_Cancel(this)
    } return this.cancel
  } set Cancel(v) {
    this.cancel = v;
  }

  Show = () => {
    this.Is_Visible = true
  }

  // Hide
  Hide = () => {
    this.IsRun_Cancel = false
    if (this.OnPreHide) {
      this.OnPreHide()
    }

    if (this.IsRun_Cancel) return

    if (!this.Is_Hover_Visible) {
      this.Is_Visible = false
    }
  }

  // Style
  ClassName = undefined

  Style = undefined

  AdditionElement = ''

  BlankStyle = 'delete__blank'

  Title = "Xc nhn lnh?"

  showHover_Button
  get ShowHover_Button() {
    return this.showHover_Button ? this.showHover_Button
      : this.Retrieve_ShowHover_Button();
  } set ShowHover_Button(v) {
    this.showHover_Button = v;
  }

  retrieve_ShowHover_Button
  get Retrieve_ShowHover_Button() {
    if (!this.retrieve_ShowHover_Button) {
      this.retrieve_ShowHover_Button =
        util.GetDefault_Retrieve_ShowHover_Button(this)
    } return this.retrieve_ShowHover_Button
  } set Retrieve_ShowHover_Button(v) {
    this.retrieve_ShowHover_Button = v;
  }

  get View() {
    return <EntDeleteView key={this.id} store={this} />
  }

  // Modal
  get WrapperView() {
    return this.Retrieve_WrapperView();
  }

  retrieve_WrapperView
  get Retrieve_WrapperView() {
    if (!this.retrieve_WrapperView) {
      this.retrieve_WrapperView = util.GetDefault_Retrieve_WrapperView(this)
    } return this.retrieve_WrapperView
  } set Retrieve_WrapperView(v) {
    this.retrieve_WrapperView = v;
  }

  modal_ButtonView
  get Modal_ButtonView() {
    if (!this.modal_ButtonView) {
      this.modal_ButtonView = util.GetDefault_Modal_ButtonView(this)
    }
    return this.modal_ButtonView
  } set Modal_ButtonView(v) {
    this.modal_ButtonView = v;
  }

  modal_InfoView
  get Modal_InfoView() {
    if (!this.modal_InfoView) {
      this.modal_InfoView = util.GetDefault_Modal_InfoView(this)
    }
    return this.modal_InfoView
  } set Modal_InfoView(v) {
    this.modal_InfoView = v;
  }

  modal_ContentView
  get Modal_ContentView() {
    if (!this.modal_ContentView) {
      this.modal_ContentView = util.GetDefault_Modal_ContentView(this)
    } return this.modal_ContentView
  } set Modal_ContentView(v) {
    this.modal_ContentView = v;
  }

  // UseStyle
  UseType = ut.mobx

  get WrapperStyle() {
    switch (this.UseType) {
      case ut.mobx:
        return undefined
      case ut.style:
        return this.Is_Visible ? undefined : { display: 'none' }
    }
  }
}