import { Item } from './Item'
import { message } from 'antd'

import * as genUtil from '../../../../../util/generalUtil'
import * as api from '../../../../../util/apiUtil'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict)

  qI._id = data._id
  qI.userId = data.userId

  return qI;
}

export const get = (dict) => {
  var qI = new Item();

  qI.Dict = dict;

  return qI;
}

// Property


// Method


// Api
export const GetData = (url, apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}