import { Image } from "./Image"

export const get = (source, alt, width, height) => {
  var qI = new Image();

  qI.Source = source;
  qI.Alt = alt;
  qI.Width = width;
  qI.Height = height;

  return qI;
}

export const GetTest = () => {
  return get(
    'https://raw.githubusercontent.com/bimtaiht/bimdev-source/master/Image/hello.png',
    'test-image',
    200, 200
  )
}