import { makeObservable, observable, action } from 'mobx';
import * as util from './ItemVMUtil'
import './Item.css'

import * as genUtil from '../../../../../util/generalUtil'

export class ItemVM {
  constructor() {
    makeObservable(this, {
      
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // User
  get userId(){
    return this.ModelItem.userId;
  } set userId(v){
    this.ModelItem.userId = v
  }

  OnSet_User_Func = []

  user
  get User(){
    if (!this.user){
      util.Handle_User(this)
    } return this.user
  } set User(v){
    this.user = v;
    this.userId = v?._id

    genUtil.Handle(this.OnSet_User_Func,
      (v) => this.OnSet_User_Func = v)
  }

  user_Select
  get User_Select() {
    if (!this.user_Select) {
      this.user_Select = util.GetUser_Select(this)
    } return this.user_Select
  }

  // Save
  Save = (handle) => {
    this.ModelItem.Save(handle)
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }

  // view
  get RowView() {
    return util.GetRowView(this)
  }
}