import * as util from './DictUtil'
import * as itemUtil from '../item/ItemUtil'
import { message } from 'antd'

import * as genUtil from '../../../../../util/generalUtil'

export class Dict {
  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  // Item
  OnSet_Items_Func = []

  items
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
    
    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = () => {
    var qI = itemUtil.get(this);

    this.Items.push(qI)
    return qI;
  }
}