import { makeObservable, observable, action } from 'mobx';
import * as rs from '../../../text/TextRenderState';
import * as editTextUtil from '../../../text/EditableTextVMUtil';
import EditableTextView from '../../../text/EditableTextView';
import * as genUtil from '../../../../util/generalUtil'

export class UserVM {
  constructor() {
    makeObservable(this, {
      renderState: observable,
    })
  }
  
  get id(){
    return this.ModelItem.id
  }

  renderState = rs._default
  get RenderState() {
    return this.renderState
  } set RenderState(v) {
    this.renderState = v;
    this.UsernameVM.RenderState = v;
    // this.DescriptionVM.RenderState = v;

    if (this.OnSet_RenderState){
      this.OnSet_RenderState(v)
    }

    if (!this.IsSaveDatabase){
      this.IsSaveDatabase = true
    } else if (v === rs._default) {
      this.SaveDatabase(false);
    }
  }

  Edit = () => {
    if (this.RenderState === rs._default) {
      this.RenderState = rs.edit
      this.Hide_Tool();
    }
  }

  // UserName
  get Username(){
    return this.ModelItem.Username
  } set Username(v){
    this.ModelItem.Username = v;
  }

  usernameVM
  get UsernameVM() {
    if (!this.usernameVM) {
      var obj = this.usernameVM = editTextUtil.get(this.Username, this.CanEdit);

      obj.Is_Tool_Visible = false
      // obj.OnKeyDown = (e) => {
      //   var { key, value } = inputUtil.Get_Shortcut(e);
      //   switch (key) {
      //     case inputUtil.enter:
      //       this.RenderState = rs._default
      //       break;
      //   }
      // }

      genUtil.Add(
        obj.OnSet_RenderState_Func_Dict,
        () => {
          if (obj.RenderState === rs._default) {
            this.Username = obj.Content;
          }
        },
        false
      )

    }
    return this.usernameVM;
  }

  get UsernameView() {
    return <EditableTextView store={this.UsernameVM} />
  }

  // Name
  get Name(){
    return this.ModelItem.Name
  } set Name(v){
    this.ModelItem.Name = v;
  }

   // Password
   get Password(){
    return this.ModelItem.Password
  } set Password(v){
    this.ModelItem.Password = v;
  }

  // Save
  IsSaveDatabase = true

  Save = (handle) => {
    this.ModelItem.Save(handle);
  }

  SaveDatabase = (showMessage = true, handle) => {
    this.ModelItem.SaveDatabase(showMessage, handle);
  }

  // Index
  get CurrentList() {
    return this.Dict.Users
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  // command
  get CommandDict(){
    return this.ModelItem.CommandDict;
  }
}