import { Field } from "./Field";
import FieldView from "./FieldView";

import * as refUtil from '../../util/refUtil'

// instance
export const get = (valueGet, valueSet, valueSet_OK, 
    storageType = 'text') => {
  var qI = new Field();

  qI.ValueGet = valueGet;
  qI.ValueSet = valueSet;
  qI.ValueSet_OK = valueSet_OK

  qI.ValueVM.StorageType = storageType

  return qI;
}

// property
// ref
export const GetRefData = (q) => {
  refUtil.ClickOutsite(q, 'Target', (e) => {
    q.EditOK()
  })

  return q.Target_RefData;
}

export const GetDefault_ValueGet = (q) => {
  return q.value;
}

export const GetDefault_ValueSet = (q, v) => {
  return q.value = v;
}

// view
export const GetDefault_ViewRender = (q) => {
  return () => <FieldView key={q.Key_id} store={q} />
}

export const Get_WrapperView = (q) => {
  return <div key={q.id}
    onMouseEnter={q.Ready}
    onMouseLeave={q.NotReady}
    onDoubleClick={q.Edit}>
      {q.View}
  </div>
}