import { makeObservable, observable, action } from 'mobx'
import { Button, message } from 'antd'
import * as util from './DictVMUtil'
import * as itemUtil from '../base/BaseVMUtil'

import * as commandType from '../../../user/command/base/CommandType'

import * as genUtil from '../../../../../../util/generalUtil'
import * as commandUtil from '../../../../../../util/commandUtil'

export class DictVM {
  constructor() {
    makeObservable(this, {
      items: observable,
      CurrentItem: observable,
    })
  }

  get id() {
    return this.ModelItem.id
  }

  get IsCreator() {
    return this.ModelItem.IsCreator
  }

  // Item
  OnSet_Items_Func = []

  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    }
    return this.items
  }
  set Items(v) {
    this.items = v

    genUtil.Handle(this.OnSet_Items_Func, (v) => (this.OnSet_Items_Func = v))
  }

  AddItem = () => {
    var q = this.ModelItem.AddItem()
    var qI = itemUtil.get(q, this)

    this.Items.push(qI)
    return qI
  }

  CurrentItem = undefined

  summary
  get Summary() {
    if (!this.summary) {
      this.summary = util.GetSummary(this)
    }
    return this.summary
  }

  get InCoin() {
    return this.ModelItem.InCoin
  }

  // validate
  Validate = (level) => {
    return commandUtil.Validate(undefined, { type: commandType.content, level })
  }

  // view
  // table
  get ItemsView() {
    return this.Items?.map((x) => x.TitleView)
  }

  get View() {
    return (
      <div className="flex">
        {
          // this.IsCreator &&
          true ? (
            <div className="mr-2">
              <Button
                type="primary"
                size="small"
                className="btn-success"
                onClick={this.AddItem}
              >
                +
              </Button>
            </div>
          ) : (
            ''
          )
        }

        <div>
          <table className="table table-bordered table-fixed">
            <thead className="bg-[#e9ecef]">
              <tr>
                <th width="30" />
                <th width="38" />
                {!this.InCoin ? <th width="80">Coin</th> : undefined}
                <th width="80">Type</th>
                <th className="w-1/4">Inital Price</th>
                <th className="w-1/4">Take Profit</th>
                <th className="w-1/4">Stop Loss</th>
                <th className="w-1/4">Risk/Reward</th>
              </tr>
            </thead>
            <tbody>
              {this.Items ? this.Summary.RowView : undefined}
              {this.Items?.map((x) => x.RowView)}
            </tbody>
          </table>
        </div>
      </div>
    )
  }
}
