import { makeObservable, observable, action } from 'mobx'
import { message } from 'antd'
import { url } from './Base_Url'
import * as util from './BaseUtil'

import * as genUtil from '../../../../../../../util/generalUtil'

export class Base {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  }
  set _id(v) {
    this.__id = v
    genUtil.Handle(
      this.OnSet_id_Func_Dict,
      (v) => (this.OnSet_id_Func_Dict = v)
    )
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID()
    }
    return this.tempId
  }

  get id() {
    return this._id ? this._id : this.TempId
  }

  addin
  get Addin() {
    if (!this.addin) {
      this.addin = this.Dict
    }
    return this.addin
  }

  // star review
  Star = {
    Rating: 4,
    Quantity: 8,
  }

  Description = 'm t ...'

  // apidata
  get ApiData() {
    var obj = {
      _id: this._id,
      description: this.Description,
      addinId: this.Addin.__id,
    }

    return obj
  }

  // Save
  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url, () => {
      if (showMessage) {
        message.success('save successfully!')
      }
      if (handle) {
        handle()
      }
    })
  }

  Save = (handle) => {
    const func = () => {
      if (handle) {
        handle()
      }
    }

    const finish = () => {
      this.OnSave = false
      func()
    }

    if (this.OnSave) {
      func()
      return
    } else {
      this.OnSave = true
    }

    this.SaveDatabase(false, finish)
  }
}
