import React, { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Input } from "antd";
import * as textVMUtil from '../text/TextVMUtil'
import * as ts from '../text/TextState'

class InputView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      var content = `Ni dung input`
      this.state = textVMUtil.get(content);
    } else {
      this.state = store;
    }

    var data = this.state;
    data.Ref = React.createRef();
  }

  onChange = (e) => {
    var { value } = e.target;

    var data = this.state;

    if (value !== '' && data.InputRegex) {
      if (!data.InputRegex.test(value)) return;
    }

    this.state.Content = value;
  }

  OnKeyDown = (e) => {
    var data = this.state;

    if (data.OnKeyDown) {
      data.OnKeyDown(e)
    }
  }

  render() {
    var data = this.state;
    var { content, placeholder } = data;
    var style = this.props.style
    var className = this.props.className
    var onFocus = this.props.onFocus ?
      this.props.onFocus : data.OnFocus

    return (
      <>
        {data.State === ts.text ?
          <Input ref={data.Ref} style={style} className={className}
            value={content} onChange={this.onChange}
            onKeyDown={this.OnKeyDown}
            onFocus={onFocus}
            placeholder={placeholder} /> : ''}
        {data.State === ts.password ?
          <Input.Password ref={data.Ref} style={style} className={className}
            value={content} onChange={this.onChange}
            onFocus={onFocus}
            onKeyDown={this.OnKeyDown} /> : ''}
      </>
    )
  }
}

export default observer(InputView)