import { makeObservable, observable, action } from 'mobx'
import * as util from './GroupVMUtil'

import * as genUtil from '../../../../../util/generalUtil'

export class GroupVM {
  constructor() {
    makeObservable(this, {
      currentItem: observable,
    })
  }

  itemDict
  get ItemDict() {
    if (!this.itemDict) {
      this.itemDict = util.GetItemDict(this)
    }
    return this.itemDict
  }

  OnSet_CurrentItem_Func = []

  currentItem = undefined
  get CurrentItem() {
    if (!this.currentItem) {
      util.Handle_CurrentItem(this)
    }
    return this.currentItem
  }
  set CurrentItem(v) {
    this.currentItem = v
    genUtil.Handle(
      this.OnSet_CurrentItem_Func,
      (v) => (this.OnSet_CurrentItem_Func = v)
    )
  }

  // view
  get View() {
    return util.GetView(this)
  }
}
