import { makeObservable, observable, action } from 'mobx';
import SelectView from './SelectView';
import * as util from './SelectVMUtil'

export class SelectVM {
  constructor() {
    makeObservable(this, {
      items: observable,
      selectedIndex: observable,
      selectedItem: observable,
      CanClear: observable,
      open: observable,
      canOpen: observable,
      isDisabled: observable,
      disabled: observable
    })
  }

  items = undefined
  get Items() {
    if (!this.items) {
      this.items = [];
    }
    return this.items;
  }
  set Items(v) {
    this.items = v
  }

  initialItem = undefined
  get InitialItem() {
    return this.initialItem;
  }
  set InitialItem(v) {
    this.IsSet_InitialItem = true;
    this.initialItem = v;
    this.InitialIndex = this.GetInitialIndex();
    this.SelectedIndex = this.InitialIndex;
  }

  initialIndex
  get InitialIndex() {
    if (!this.initialIndex) {
      this.initialIndex = this.GetInitialIndex();
    }
    return this.initialIndex;
  }
  set InitialIndex(v) {
    this.initialIndex = v;
  }

  GetInitialIndex = () => {
    var index = this.Items.indexOf(this.InitialItem);
    return index !== -1 ? index : null;
  }

  selectedIndex = undefined
  get SelectedIndex() {
    return this.selectedIndex;
  }
  set SelectedIndex(v) {
    this.selectedIndex = v;
    this.SelectedItem = this.GetSelectedItem();
  }

  selectedItem = undefined
  get SelectedItem() {
    if (!this.selectedItem) {
      this.selectedItem = this.GetSelectedItem();
    }
    return this.selectedItem;
  }
  set SelectedItem(v) {
    this.selectedItem = v;

    if (this.IsSet_InitialItem) {
      this.IsSet_InitialItem = false
    } else if (this.OnSelectedItemChanged) {
      this.OnSelectedItemChanged(v);
    }
  }

  SetValue = (v) => {
    this.InitialItem = this.SelectedItem = v;
  }

  GetSelectedItem = () => {
    var index = this.SelectedIndex;
    if (index === -1) return undefined;
    return this.Items[index];
  }

  placeholder
  get Placeholder() {
    if (!this.placeholder) {
      this.placeholder = "chn i tng ...";
    }
    return this.placeholder;
  }
  set Placeholder(v) {
    this.placeholder = v;
  }

  CanClear = false

  Clear = () => {
    this.SelectedIndex = null
  }

  get DivStyle() {
    return this.CanClear ? {
      position: 'relative',
      padding: '0 22px 0 0'
    } : {};
  }

  titleRender
  get TitleRender() {
    if (!this.titleRender) {
      this.titleRender = util.GetTitleRender(this);
    }
    return this.titleRender;
  }
  set TitleRender(v) {
    this.titleRender = v;
  }

  open = false
  get Open() {
    return this.open;
  }
  set Open(v) {
    this.open = v;
    if (this.OnSetOpen) {
      this.OnSetOpen(v);
    }
  }

  canOpen = true
  get CanOpen() {
    return this.canOpen;
  } set CanOpen(v) {
    this.canOpen = v;
  }

  isDisabled = false
  get IsDisabled() {
    return this.isDisabled;
  } set IsDisabled(v) {
    this.isDisabled = v;
  }

  // disabled
  disabled = false
  get Disabled() {
    if (this.GetDisabled_Func) {
      return this.GetDisabled_Func();
    }
    return this.disabled
  } set Disabled(v) {
    this.disabled = v;
  }

  IdRender

  // View
  get View() {
    return <SelectView store={this} />
  }
}