import { GroupVM } from './GroupVM'
import * as itemDictUtil from '../base/dict/DictVMUtil'
import * as baseUtil from '../../coin/base/base/BaseVMUtil'

import * as genUtil from '../../../../../util/generalUtil'

// Instance
export const get = (q, dict) => {
  var qI = new GroupVM()
  qI.ModelItem = q
  qI.Dict = dict
  return qI
}

// Property
export const GetItemDict = (q) => {
  return itemDictUtil.get(q.ModelItem.ItemDict, q)
}

export const Handle_CurrentItem = (q) => {
  if (q.IsGet_CurrentItem) return undefined
  q.IsGet_CurrentItem = true

  const setObj = (data) => {
    q.CurrentItem = baseUtil.get(data, q)
    q.IsGet_CurrentItem = false
  }

  var host = q.ModelItem
  if (host.CurrentItem) {
    setObj(host.CurrentItem)
  } else {
    genUtil.Add_2(host.OnSet_CurrentItem_Func, () => setObj(host.CurrentItem))
  }
}

// view
export const GetView = (q) => {
  return (
    <div>
      {/* Owner */}
      <div>
        <div className="text-lg font-medium">Owned</div>

        <div className="mt-2">{q.Owner.View}</div>
      </div>

      {/* Buy */}
      <div>
        <div className="text-lg font-medium">Buy</div>

        <div className="mt-2">{q.Buy.View}</div>
      </div>

      {/* Creator */}
      <div className="mt-4">
        <div className="text-lg font-medium">Created</div>

        <div className="mt-2">{q.Creator.View}</div>
      </div>

      {/* Store */}
      <div className="mt-4">
        <div className="text-lg font-medium">Store</div>

        <div className="mt-2">{q.Store.View}</div>
      </div>
    </div>
  )
}
