import { makeObservable, observable, action } from 'mobx';
import * as util from './DictVMUtil'
import * as itemUtil from '../item/ItemVMUtil'
import { Button, message } from 'antd';
import TableView from '../table/TableView';

import * as genUtil from '../../../../../util/generalUtil'

export class DictVM {
  constructor() {
    makeObservable(this, {
      items: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Items
  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;
  }

  AddItem = () => {
    var q = this.ModelItem.AddItem();
    var qI = itemUtil.get(q, this);

    this.Items.push(qI)
    return qI;
  }

  // table
  get TableView() {
    return <TableView store={this} />
  }

  get View() {
    return <div>
      <div className='f-jc text-lg font-medium'>
        <span>Admin</span>
      </div>
      <div className='f'>
        <div className='admin__pgc__add'>
          <Button type='primary' size='small'
            className='btn-success'
            onClick={this.AddItem}>+</Button>
        </div>
        <div className='es__main f1 ml-2'>
          <TableView store={this} />
        </div>
      </div>
    </div>
  }
}