import { url } from './Base_Url'
import { Base } from './Base'
import * as infoUtil from '../info/base/BaseUtil'
import * as tradeGroupUtil from '../../../trade/group/GroupUtil'

import * as api from '../../../../../../util/apiUtil'
import * as genUtil from '../../../../../../util/generalUtil'

import { PMData } from '../../../../single/model/PMData'

const pmData = () => PMData.Instance

// Instance
export const GetFromData = (data, dict) => {
  var qI = get(dict)

  qI._id = data._id
  qI.CreatedDate = data.createdDate

  qI.Code = data.code
  qI.Name = data.name
  qI.Reference_RatioName = data.ref_rationame

  return qI
}

export const get = (dict) => {
  var qI = new Base()

  qI.Dict = dict

  return qI
}

export const Clone = (q, dict) => {
  var qI = get(dict)

  qI._id = q._id
  qI.Name = q.Name

  return qI
}

// Property
// trade
export const GetTradeGroup = (q) => {
  var qI = tradeGroupUtil.get(q)
  qI.Coin = q
  return qI
}

// info
export const Handle_Info = (q) => {
  if (q.IsGet_Info) return
  q.IsGet_Info = true

  const setObj = (data) => {
    q.Info = data ? infoUtil.GetFromData(data, q) : infoUtil.get(q)
    q.IsGet_Info = false
  }

  var apiData = {
    addinId: q._id,
  }

  if (q._id) {
    infoUtil.GetOne(apiData, (data) => setObj(data))
  } else {
    q.Save(() => setObj())
  }
}

export const Handle_IsOwned = (q) => {
  if (q.IsGet_IsOwned) return
  q.IsGet_IsOwned = true

  const setObj = (data) => {
    q.IsOwned = data
    q.IsGet_IsOwned = false
  }

  var userDict = pmData().UserDict

  const func = () => {
    api
      .post('https://api.bimdev.vn/api/ba_addinvalidate/check', {
        addinId: q._id,
        userId: userDict.loginId,
      })
      .then((res) => res.data)
      .then((data) => setObj(data))
  }

  if (!userDict.loginId) {
    setObj(false)
  } else {
    func()
  }

  genUtil.Add_2(userDict.OnSet_loginId_Func, func, false)
}

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData)

  apiFunc()
    .then((response) => response.data)
    .then((data) => {
      if (handle) {
        handle(data)
      }
    })
}

export const GetOne = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/get`, apiData)

  apiFunc()
    .then((response) => response.data)
    .then((data) => {
      if (handle) {
        handle(data)
      }
    })
}
