import InputView from "../input/InputView";
import * as rs from './TextRenderState'
import { TextRenderItem } from "./TextRenderItem";
import { Button } from "antd";

import * as htmlUtil from '../../util/htmlUtil'
import QuillEditorView from "../editor/QuillEditorView";

export const get = (state, render) => {
  var qI = new TextRenderItem();
  qI.State = state;
  qI.Render = render;
  return qI;
}

export const GetRender = (q) => {
  switch (q.State) {
    case rs._default:
      return (content, context) => {
        var className = context.ClassName;
        var style = context.Style;
        var onDoubleClick = context.OnDoubleClick
        var onClick = context.OnClick

        var storageType_css = `${context.StorageType}`

        return <>
          <div 
            className={`${className} input_wrapper view ${storageType_css}`} 
            style={style}
            onDoubleClick={onDoubleClick}
            onClick={onClick}>

            {context.StorageType === 'text' ?
              content :
              <div dangerouslySetInnerHTML={htmlUtil.createMarkup(content)} />
            }

          </div>
          {context.Is_Tool_Visible && context.Is_Edit_Visible ?
            <Button type='primary' size='small'
              className='left10 btn-warning' onClick={context.Edit}
            >e</Button>
            : ''}
        </>
      }
    case 'ready':
    case rs.edit:
      return (content, context) => {
        var className = context.ClassName;
        var style = context.Style;
        var onFocus = context.OnFocus

        // onfocus
        var r_onFocus = () => {
          context.Edit();
          if (onFocus) {
            onFocus();
          }
        }

        return <>
          <div className={`input__wrapper edit ${className}`}>

            {context.StorageType === 'text' ?
              <InputView ref={context.Ref}
                className='input__main'
                style={style}
                store={context}
                ref={context.Ref}
                onFocus={r_onFocus} />
              :
              <QuillEditorView store={context.Editor}
                onFocus={r_onFocus} />
            }

            {context.Is_Tool_Visible && context.Is_OK_Visible ?
              <Button type='primary' size='small'
                onClick={context.EditOK}>ok</Button>
              : ''}
          </div>
        </>
      }
    default:
      return undefined;
  }
}