import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../../../util/generalUtil'
import * as util from './HeaderUtil'
import * as vs from '../../view/ViewState'
import { PMDataVM } from '../../../single/form/PMDataVM';
import ImageView from '../../../../image/ImageView'
import * as imgUtil from '../../../../image/ImageUtil'

const pmDataVM = () => PMDataVM.Instance

export class Header {
  constructor() {
    makeObservable(this, {

    })
  }

  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  // Data
  get MainMenu() {
    return pmDataVM().MainMenu;
  }

  get LogoView() {
    var img = imgUtil.get('https://bimdev.vn/wp-content/uploads/2020/01/logo.jpg')
    return <ImageView className='header__logo' store={img} />
  }
}