import { ItemVM } from './ItemVM'
import { EntDelete } from '../../../../delete/EntDelete';
import { SelectVM } from '../../../../select/SelectVM';

import * as genUtil from '../../../../../util/generalUtil'

// Instance
export const get = (q, dict) => {
  var qI = new ItemVM();

  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

// Property
export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = 'manual'
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

// User
export const Handle_User = (q) => {
  if (q.IsGet_User) return undefined;
  q.IsGet_User = true;


  const setObj = (data) => {
    var list = data;

    var _id = q.userId;
    if (_id) {
      q.User = genUtil.SingleFilter(list, (x) => x.id === _id);
    } else {
      q.User = undefined;
    }

    q.IsGet_User = false;
  }

  var host = q.ModelItem.Dict.Dict.Dict
  if (host.Users) {
    setObj(host.Users)
  } else {
    genUtil.Add_2(
      host.OnSet_Users_Func,
      () => setObj(host.Users)
    )
  }
}

export const GetUser_Select = (q) => {
  var qI = new SelectVM();

  const setObj = (data) => {
    var list = qI.Items = data;

    const setObj2 = () => {
      qI.InitialItem = q.User;
    }

    if (q.User) {
      setObj2();
    } else {
      genUtil.Add_2(q.OnSet_User_Func,
        setObj2)
    }
  }

  var host = q.ModelItem.Dict.Dict.Dict
  if (host.Users) {
    setObj(host.Users)
  } else {
    genUtil.Add_2(
      host.OnSet_Users_Func,
      () => setObj(host.Users)
    )
  }

  qI.Placeholder = 'chn user...'
  qI.TitleRender = (v) => v.Username
  qI.OnSelectedItemChanged = (v) => {
    q.User = v
    q.Save()
  };

  return qI;
}

// view
export const GetRowView = (q) => {
  return <tr key={q.id}>
    <td>
      <div className='w-40'>
        {q.User_Select.View}
      </div>
    </td>
    <td>
      <div className='f-jc'>
        <div className='f'>
          {q.DeleteVM.View}
        </div>
      </div>
    </td>
  </tr>
}