import { makeObservable, observable, action } from 'mobx';
import { message } from 'antd';
import * as util from './ItemVMUtil'
import './Item.css'
import EditableTextView from '../../../../../text/EditableTextView';

import * as genUtil from '../../../../../../util/generalUtil'
import * as inputUtil from '../../../../../../util/inputUtil'

export class ItemVM {
  constructor() {
    makeObservable(this, {

    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Name
  get Name() {
    return this.ModelItem.Name;
  } set Name(v) {
    this.ModelItem.Name = v;
  }

  nameVM
  get NameVM() {
    if (!this.nameVM) {
      this.nameVM = inputUtil.GetInput(this, 'Name', () => this.Save());
    }
    return this.nameVM;
  }

  get NameView() {
    return <EditableTextView className='mgs__cell__main'
      store={this.NameVM} />
  }

   // command
   commands
   get Commands(){
     if (!this.commands){
       this.commands = util.GetCommands(this)
     } return this.commands;
   }
 

  // row
  get RowView(){
    return util.GetRowView(this)
  }

  // save
  Save = (handle) => {
    this.ModelItem.Save(handle)
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete();
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }
}