import { makeObservable, observable, action } from 'mobx';
import * as ts from './TextState'

export class TextVM {
  constructor() {
    makeObservable(this, {
      content: observable,
      placeholder : observable,
      State : observable,
      ClassName : observable
    })
  }

  GetContentFunc

  content = undefined
  get Content(){
    return this.content;
  }
  set Content(v) {
    this.content = v;
    if (this.onSetContent) {
      this.onSetContent(v);
    }

    if (this.OnSetContent) {
      this.OnSetContent(v);
    }
  }

  placeholder = "ni dung input"
  set Placeholder(v) {
    this.placeholder = v;
  }

  inputStyle

  style
  get Style() {
    if (!this.style) {
      this.style = this.inputStyle ? this.inputStyle : {};

      if (this.wrapper) {
        this.style.position = 'relative';
      }
    }
    return this.style;
  }

  State = ts.text

  ClassName = undefined
}