import React, { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { QuillEditor } from "./QuillEditor";
import ReactQuill, { Quill } from "react-quill";
import './QuillEditor.css'
import 'react-quill/dist/quill.snow.css'; // ES6

class QuillEditorView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new QuillEditor();
    } else {
      this.state = store;
    }

    var data = this.state;
    data.Ref = React.createRef();
  }

  componentDidMount() {
    var data = this.state;

    if (data.IsFocusInit) {
      data.Ref.current.focus();
    }
  }

  OnChange = (content) => {
    var data = this.state;
    data.LastContent = data.Content;
    data.Content = content;
  }

  OnKeyDown = (e) => {
    var data = this.state;
    if (data.OnKeyDown) {
      data.OnKeyDown(e)

      if (data.IsCancel) {
        data.IsCancel = false
        data.Content = data.LastContent
      }
      return;
    }

    // Alt + Enter
    if (e.altKey && e.keyCode == 13) {
      if (data.OnAltEnter) {
        e.preventDefault();
        data.OnAltEnter();
      }
    }
    // Backspace
    else if (e.keyCode == 8 || e.charCode == 46) {
      if ((!data.LastContent && data.Content.length === 0) ||
        (data.LastContent === data.Content && data.Content === '<p><br></p>')) {
        // ang b 1 li nh khi nhp 1 ch ci, sau  backspace
        if (data.OnEmptyBackspace) {
          e.preventDefault();
          data.OnEmptyBackspace();
        }
      }
    }
    // Tab
    else if (e.keyCode == 9) {
      if (data.OnTab) {
        e.preventDefault();
        data.OnTab();
      }
    }

    data.LastContent = data.Content;
  }

  render() {
    var data = this.state;

    var modules = {
      syntax: true,
      toolbar: {
        container: `#${data.id}`,
        //id ="toorbar"    B I U S I V F P   . 
        handlers: data.Handles
      },
      clipboard: {
        // toggle to add extra line breaks when pasting HTML:
        matchVisual: false,
      },
    };

    var formats = [
      'header',
      'bold', 'italic', 'underline', 'strike',
      'image', 'video', 'file', 'link', "code-block", "video", "blockquote", "clean"
    ];

    data.OnKeyDown = data.OnKeyDown ?
      data.OnKeyDown : this.props.onKeyDown
    var className = this.props.className ? this.props.className : ''
    var style = this.props.style
    var onFocus = this.props.onFocus ?
      this.props.onFocus : data.OnFocus

    return (
      <div className={`ql__wrapper ${className}`} style={style}>
        {/* {container} */}
        <div className={data.IsHaveTool ? '' : 'hidden'}>
          {data.Tool_Container}
        </div>

        <ReactQuill ref={data.Ref}
          className='ql__style'
          // theme='snow'
          modules={modules}
          formats={formats}
          onChange={this.OnChange}
          value={data.Content}
          placeholder={data.PlaceHolder}
          onKeyDown={data.OnKeyDown}
          onFocus={onFocus}
        >
        </ReactQuill>
      </div>
    )
  }
}

export default observer(QuillEditorView)