import { EditableTextVM } from "./EditableTextVM"
import * as editorUtil from '../editor/QuillEditorUtil'

export const get = (content, canEdit = true, isToolVisible) => {
  var qI = new EditableTextVM();
  
  qI.Content = content;
  qI.CanChange_RenderState = canEdit;

  if (isToolVisible === undefined){
    isToolVisible = canEdit;
  }

  qI.Is_Tool_Visible = isToolVisible;

  return qI;
}

// property
export const GetEditor = (q) => {
  var qI = editorUtil.get(q.Content, q.Placeholder)

  qI.OnSetContent = (v) => q.Content = v;
  qI.OnKeyDown = q.OnKeyDown
  qI.IsHaveTool = false

  return qI;
}