import {Command} from './Command'
import * as commandLevel from './CommandLevel'

// instance
export const GetFromData = (data, dict) => {
  var qI = get(dict)

  qI.Type = data.type
  qI.Level = data.level

  return qI;  
}

export const get = (dict) => {
  var qI = new Command();
  qI.Dict = dict;
  return qI;
}

// method
export const Validate = (q, data) => {
  return q.Type === data.type && 
    commandLevel.GetOrder(q.Level) >= commandLevel.GetOrder(data.level);
}