import { makeObservable, observable, action } from 'mobx'
import { message } from 'antd'
import { url } from './Base_Url'
import * as util from './BaseUtil'
import { PMData } from '../../../../single/model/PMData'

import * as genUtil from '../../../../../../util/generalUtil'

const pmData = () => PMData.Instance

export class Base {
  constructor() {
    makeObservable(this, {
      coin: observable,
      TakeProfit_Percent: observable,
      StopLoss_Percent: observable,
      IsSubmit: observable,
      EarnPercent: observable,
    })
  }

  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  }
  set _id(v) {
    this.__id = v
    genUtil.Handle(
      this.OnSet_id_Func_Dict,
      (v) => (this.OnSet_id_Func_Dict = v)
    )
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID()
    }
    return this.tempId
  }

  get id() {
    return this._id ? this._id : this.TempId
  }

  Type = 'long'

  InitialPrice = 40000

  takeProfit_Price = undefined
  get TakeProfit_Price() {
    if (this.takeProfit_Price === undefined) {
      this.takeProfit_Price = util.GetTakeProfit_Price(this)
    }
    return this.takeProfit_Price
  }
  set TakeProfit_Price(v) {
    this.takeProfit_Price = v
  }

  TakeProfit_Percent = 0.03

  stopLoss_Price = undefined
  get StopLoss_Price() {
    if (this.stopLoss_Price === undefined) {
      this.stopLoss_Price = util.GetStopLoss_Price(this)
    }
    return this.stopLoss_Price
  }
  set StopLoss_Price(v) {
    this.stopLoss_Price = v
  }

  StopLoss_Percent = 0.01

  IsSubmit = undefined

  Submit = (type) => {
    this.IsSubmit = true
    this.EarnPercent =
      1 * (type === 'profit' ? this.TakeProfit_Percent : -this.StopLoss_Percent)

    this.Save()
  }

  EarnPercent = undefined

  get RewardRisk_Ratio() {
    return this.TakeProfit_Percent / this.StopLoss_Percent
  }

  _coinId
  get coinId() {
    if (!this._coinId) {
      this._coinId = this.Coin._id
    }
    return this._coinId
  }
  set coinId(v) {
    this._coinId = v
  }

  get InCoin() {
    return this.Dict.InCoin
  }

  coin = undefined
  get Coin() {
    if (!this.coin) {
      if (this.InCoin) {
        this.coin = this.Dict.Coin
      } else {
        util.Handle_Coin(this)
      }
    }
    return this.coin
  }
  set Coin(v) {
    this.coin = v
  }

  // s hu
  isOwned = undefined
  get IsOwned() {
    if (this.isOwned === undefined) {
      util.Handle_IsOwned(this)
    }
    return this.isOwned
  }
  set IsOwned(v) {
    this.isOwned = v
  }

  //
  get IsOwner() {
    return this.creatorId === pmData().UserDict.loginId
  }

  // clone
  Clone = (dict) => {
    return util.Clone(this, dict)
  }

  // apidata
  get ApiData() {
    var obj = {
      _id: this._id,
      coinId: this.coinId,
      type: this.Type,
      initialprice: this.InitialPrice,
      takeprofitprecent: this.TakeProfit_Percent,
      stoplosspercent: this.StopLoss_Percent,

      issubmit: this.IsSubmit,
      earnpercent: this.EarnPercent,
    }

    return obj
  }

  // Save
  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url, () => {
      if (showMessage) {
        message.success('save successfully!')
      }
      if (handle) {
        handle()
      }
    })
  }

  Save = (handle) => {
    const func = () => {
      if (handle) {
        handle()
      }
    }

    const finish = () => {
      this.OnSave = false
      func()
    }

    if (this.OnSave) {
      func()
      return
    } else {
      this.OnSave = true
    }

    console.log('save')
    this.SaveDatabase(false, finish)
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.DeleteDatabase(false)
  }

  DeleteDatabase = (showMessage = true, handle) => {
    genUtil.DeleteDatabase(this, url, () => {
      if (showMessage) {
        message.success('delete successfully!')
      }
      if (handle) {
        handle()
      }
    })
  }
}
