import { Target } from "./Target"
import * as tt from './TargetType'

import * as api from '../../../../../../../util/apiUtil'
import * as genUtil from '../../../../../../../util/generalUtil'

import { PMData } from "../../../../../single/model/PMData"

const pmData = () => PMData.Instance;

// instance
export const GetFromData = (data, dict) => {
  var qI = get(dict)

  if (!data) {
    qI.Type = tt.general
  }

  return qI;
}

export const get = (dict) => {
  var qI = new Target();
  qI.Dict = dict;
  return qI;
}

// property
export const GetQueryData = (q) => {
  var qI = {}
  var dict = q.Dict;

  // user
  switch (q.Type) {
    default:
      qI.userId = dict.User?._id
      break;
  }

  return qI;
}

export const GetRoleUrl = (q) => {
  switch (q.Type) {
    case tt.general:
      return "https://api.bimdev.vn/api/btr_userrole"
  }
}

export const Handle_Role = (q) => {
  if (q.IsGet_Role) return undefined
  q.IsGet_Role = true

  const setObj = (data) => {
    q.Role = data
    q.IsGet_Role = false
  }

  if (!q.QueryData.userId){
    setObj(undefined)
    return
  }

  const rolename_func = (rolename) => {
    rolename = rolename ? rolename : 'User'
    pmData().RoleDict.GetItem(rolename, (item) => setObj(item));
  }

  var url = q.RoleUrl
  var apiData = q.QueryData

  const apiFunc = () => api.post(`${url}/get`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      rolename_func(data?.role)
    })
}

// method
export const IsEqual = (q1, q2) => {
  return q1.Type === q2.Type && q1.Value === q2.Value
}

export const Validate = (q, commandData) => {
  if (!q.Commands) return false;
  
  var commands = q.Commands;

  var isValid = false;
  commands.forEach(x => {
    if (!isValid) {
      isValid = x.Validate(commandData)
    }
  })

  return isValid;
}