import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { EntDelete } from './EntDelete';
import { Button, Popover } from 'antd';
import './EntDelete.css'
import * as vt from './EntDelete_VisibleType'
import * as ut from './EntDelete_UseType'

class EntDeleteView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new EntDelete()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    data.ClassName = data.ClassName ? data.ClassName :
      (this.props.className ? this.props.className : '')
    data.Style = data.Style ? data.Style : this.props.style

    return (
      <>
        {data.UseType === ut.style || data.Is_Visible ?
          <>
            <div style={data.WrapperStyle}>
              {/* Popover */}
              {data.WrapperView}

              {/* Addition Element */}
              {data.AdditionElement}
            </div>
          </>
          : (data.VisibleType === vt.hidden ?
            <div className={data.BlankStyle} />
            :
            '')
        }
      </>
    );
  }
}

export default observer(EntDeleteView);
