import * as api from '../../../../util/apiUtil'
import { PMData } from '../model/PMData'
import { PMDataVM } from './PMDataVM'
import * as genUtil from '../../../../util/generalUtil'
import * as layoutUtil from '../../entity/layout/layout/LayoutUtil'

// user
import * as userDictUtil from '../../../user/dict/User_DictVMUtil'

// role
import * as roleDictUtil from '../../entity/user/role/dict/DictVMUtil'

// coin
import * as coinGroupUtil from '../../entity/coin/group/GroupVMUtil'

// trade
import * as tradeGroupUtil from '../../entity/trade/group/GroupVMUtil'

export const pmData = () => PMData.Instance

// Instance
export const get = () => {
  var qI = new PMDataVM()

  return qI
}

// Property
// layout
export const GetLayout = (q) => {
  return layoutUtil.get()
}
// user
export const GetUserDict = (data) => {
  var qI = userDictUtil.get(pmData().UserDict, data)

  qI.ViewRender = () => (
    <div className="ud__wrapper">
      <div className="ud__dec f">
        <div className="f1" />
        <div>
          <div className="ud__main__wrapper">{qI.MainView}</div>
          {qI.ModalView}
        </div>
      </div>
    </div>
  )

  return qI
}

// role
export const GetRoleDict = (data) => {
  return roleDictUtil.get(pmData().RoleDict, data)
}

// coin
export const GetCoinGroup = (q) => {
  return coinGroupUtil.get(pmData().CoinGroup, q)
}

// trade
export const GetTradeGroup = (q) => {
  return tradeGroupUtil.get(pmData().TradeGroup, q)
}
