import { makeObservable, observable, action } from 'mobx'
import { Button, message } from 'antd'
import * as util from './BaseVMUtil'

import * as genUtil from '../../../../../../util/generalUtil'

export class BaseVM {
  constructor() {
    makeObservable(this, {
      info: observable,
    })
  }

  get id() {
    return this.ModelItem.id
  }

  // data
  fields
  get Fields() {
    if (!this.fields) {
      this.fields = util.GetFields(this)
    }
    return this.fields
  }

  GetField = (name) => {
    return genUtil.SingleFilter(this.Fields, (x) => x.Key === name)
  }

  // trade
  tradeGroup
  get TradeGroup() {
    if (!this.tradeGroup) {
      this.tradeGroup = util.GetTradeGroup(this)
    }
    return this.tradeGroup
  }

  // info
  OnGet_Info_Func = []

  info = undefined
  get Info() {
    if (!this.info) {
      util.Handle_Info(this)
    }
    return this.info
  }
  set Info(v) {
    this.info = v

    genUtil.Handle(this.OnGet_Info_Func, (v) => (this.OnGet_Info_Func = v))
  }

  get IsOwned() {
    return this.ModelItem.IsOwned
  }

  // creator
  get IsCreator() {
    return this.Dict.IsCreator
  }

  get IsOwner() {
    return this.IsCreator || this.ModelItem.IsOwner
  }

  // css
  get Css() {
    return this === this.Dict.CurrentItem
      ? 'bg-[#52c41a90] text-white font-medium'
      : ''
  }

  // current
  SetCurrent = () => {
    var dict = this.Dict

    if (dict.CurrentItem === this) {
      dict.CurrentItem = undefined
    } else {
      dict.CurrentItem = this
    }
  }

  // save
  Save = (handle) => {
    this.ModelItem.Save(handle)
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete()
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    }
    return this.deleteVM
  }

  // validate
  Validate = (level) => {
    return this.Dict.Validate(level)
  }

  // view
  get TitleView() {
    return util.GetTitleView(this)
  }

  get ContentView() {
    return util.GetContentView(this)
  }
}
