import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../../../util/generalUtil'
import * as util from './AdminVMUtil'

export class AdminVM {
  constructor() {
    makeObservable(this, {
      viewRender : observable
    })
  }

  // Users
  OnSet_Users_Func = []

  get Users(){
    return this.Dict.Users
  }


  // Dict
  Dict

  adminUser_Dict
  get AdminUser_Dict(){
    if (!this.adminUser_Dict){
      this.adminUser_Dict = 
        util.GetAdminUser_Dict(this)
    } return this.adminUser_Dict
  }

  // Item
  userManager_Dict
  get UserManager_Dict(){
    if (!this.userManager_Dict){
      this.userManager_Dict = util.GetUserManager_Dict(this)
    } return this.userManager_Dict;
  }

  // View
  viewRender = undefined
  get ViewRender(){
    if (!this.viewRender){
      this.viewRender = util.GetDefault_ViewRender(this)
    } return this.viewRender;
  } set ViewRender(v){
    this.viewRender = v;
  }

  get View(){
    return this.ViewRender();
  }
}