import * as util from './PMDataUtil'
import * as genUtil from '../../../../util/generalUtil'
import * as vm from '../../entity/view/ViewMode'

export class PMData {
  constructor() {}

  static instance
  static get Instance() {
    if (!this.instance) {
      this.instance = util.get()
    }
    return this.instance
  }
  static set Instance(v) {
    this.instance = v
  }

  //
  IsPublish = false

  // route
  routeData = undefined
  get RouteData() {
    if (!this.routeData) {
      this.routeData = [{ type: 'home' }]
    }
    return this.routeData
  }
  set RouteData(v) {
    this.routeData = v
  }

  // view
  ViewMode = vm.view

  // Data
  // user
  userDict
  get UserDict() {
    if (!this.userDict) {
      this.userDict = util.GetUserDict(this)
    }
    return this.userDict
  }

  // role
  roleDict
  get RoleDict() {
    if (!this.roleDict) {
      this.roleDict = util.GetRoleDict(this)
    }
    return this.roleDict
  }

  // Command
  commandDict
  get CommandDict() {
    if (!this.commandDict) {
      this.commandDict = util.GetCommandDict(this)
    }
    return this.commandDict
  }

  // coin
  coinGroup
  get CoinGroup() {
    if (!this.coinGroup) {
      this.coinGroup = util.GetCoinGroup(this)
    }
    return this.coinGroup
  }

  tradeGroup
  get TradeGroup() {
    if (!this.tradeGroup) {
      this.tradeGroup = util.GetTradeGroup(this)
    }
    return this.tradeGroup
  }
}
