export const content = 'content'
export const answer = 'answer'

export const List = () => {
  return [
    content,
    answer
  ]
}

export const GetName = (key) => {
  var name

  switch (key) {
    case content:
      name = 'Ni dung'
      break
    case answer:
      name = 'Tr li';
      break;
  }

  return name;
}