import React from 'react';
import * as genUtil from '../../util/generalUtil'

// method
export const GetItem = (q, name) => {
  var list = q.Items;

  var item = genUtil.SingleFilter(list, x => x.key === name)
  if (!item){
    item = {
      key: name,
      value : React.createRef()
    }

    list.push(item)
  }

  return item;
}