import { StarFilled, StarOutlined } from '@ant-design/icons'
import { BaseVM } from './BaseVM'
import * as f from '../field/Field'
import * as fieldUtil from '../../../../../../field/FieldUtil'

import * as inputUtil from '../../../../../../../util/inputUtil'
import { PMDataVM } from '../../../../../single/form/PMDataVM'
import { Switch } from 'antd'

const pmDataVM = () => PMDataVM.Instance

// Instance
export const get = (q, dict) => {
  var qI = new BaseVM()
  qI.ModelItem = q
  qI.Dict = dict
  return qI
}

// Property
// field
export const GetFields = (q) => {
  var inputs = f.List()

  var host = q.ModelItem
  var fields = []

  inputs.forEach((x, i) => {
    var fName = f.GetName(x)

    var qI
    qI = fieldUtil.get(
      () => host[fName],
      (v) => {
        host[fName] = v
      },
      q.Save,
      'html'
    )

    // qI.PreReady = () => q.Validate('edit') && pmDataVM().ViewMode === 'edit'
    // qI.PreEdit = () => q.Validate('edit')

    var validate = () => q.IsOwner
    qI.PreReady = () => {
      return validate() && pmDataVM().ViewMode === 'edit'
    }
    qI.PreEdit = validate

    qI.Key = x
    fields.push(qI)
  })

  return fields
}

// star review
export const GetStarView = (q) => {
  var elems = []

  var star = q.Star
  var rating = star.Rating

  for (let i = 0; i < 5; i++) {
    elems.push(
      <div className="mr-1 text-yellow-300" key={i}>
        {i <= rating - 1 ? <StarFilled /> : <StarOutlined />}
      </div>
    )
  }

  return (
    <div className="flex items-center">
      <div className="flex">{elems}</div>
      <div className="ml-2 mt-1 font-medium">{star.Quantity} Reviews</div>
    </div>
  )
}

// view
export const GetView = (q) => {
  return (
    <>
      <div>
        {/* <div className="my-2">{q.StarView}</div> */}

        <div className="my-2 -ml-4">
          {q.GetField(f.description).WrapperView}
        </div>
      </div>
    </>
  )
}
