import { LinkOutlined } from '@ant-design/icons'
import { Link } from 'react-router-dom'
import { BaseVM } from './BaseVM'
import { EntDelete } from '../../../../../delete/EntDelete'
import * as f from '../field/Field'
import * as fieldUtil from '../../../../../field/FieldUtil'
import * as infoUtil from '../info/base/BaseVMUtil'
import * as tradeGroupUtil from '../../../trade/group/GroupVMUtil'

import * as inputUtil from '../../../../../../util/inputUtil'
import * as genUtil from '../../../../../../util/generalUtil'

import { PMDataVM } from '../../../../single/form/PMDataVM'
import { Switch } from 'antd'

const pmDataVM = () => PMDataVM.Instance

// Instance
export const get = (q, dict) => {
  var qI = new BaseVM()
  qI.ModelItem = q
  qI.Dict = dict
  return qI
}

// Property
// field
export const GetFields = (q) => {
  var inputs = f.List()

  var host = q.ModelItem
  var fields = []

  inputs.forEach((x, i) => {
    var fName = f.GetName(x)

    var qI
    qI = fieldUtil.get(
      () => host[fName],
      (v) => {
        host[fName] = v
      },
      q.Save
    )

    // qI.PreReady = () => q.Validate('edit') && pmDataVM().ViewMode === 'edit'
    // qI.PreEdit = () => q.Validate('edit')

    var validate = () => true
    qI.PreReady = () => {
      return validate() && pmDataVM().ViewMode === 'edit'
    }
    qI.PreEdit = validate

    qI.Key = x
    fields.push(qI)
  })

  return fields
}

export const GetDeleteVM = (q) => {
  var qI = new EntDelete()

  qI.ToggleType = 'manual'
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete()
  }

  return qI
}

// trade
export const GetTradeGroup = (q) => {
  var qI = tradeGroupUtil.get(q.ModelItem.TradeGroup, q)
  return qI
}

// info
export const Handle_Info = (q) => {
  if (q.IsGet_Info) return
  q.IsGet_Info = true

  const setObj = (data) => {
    q.Info = infoUtil.get(data, q)
    q.IsGet_Info = false
  }

  var host = q.ModelItem

  if (host.Info) {
    setObj(host.Info)
  } else {
    genUtil.Add_2(host.OnGet_Info_Func, () => setObj(host.Info))
  }
}

// view
export const GetTitleView = (q) => {
  return (
    <div key={q.id}>
      {/* Title */}
      <div className={`flex items-center ${q.Css}`}>
        <div className="mx-1">
          {
            // q.Validate('edit') &&
            pmDataVM().ViewMode === 'edit' || q.GetField(f.name).OnEdit
              ? q.DeleteVM.View
              : ''
          }
        </div>

        <div className="flex items-center">
          <Link to={`./coin/${q.id}`} className="mx-1 -mt-1">
            <LinkOutlined />
          </Link>
          <div>{q.GetField(f.name).WrapperView}</div>
          <div>{q.GetField(f.code).WrapperView}</div>
        </div>
      </div>
    </div>
  )
}

export const GetContentView = (q) => {
  return (
    <>
      <div>
        <div key={q.id} className="-ml-2">
          {/* <span className="h4">{q.GetField(f.name).Value}</span> */}
          <div className="h4">{q.GetField(f.name).WrapperView}</div>

          <div className="h5">{q.GetField(f.code).WrapperView}</div>

          <div className="h5">{q.GetField(f.ref_rationame).WrapperView}</div>
        </div>

        <div className="mt-4">{q.TradeGroup.View}</div>
      </div>
    </>
  )
}
