import { QuillEditor } from "./QuillEditor"
import * as toolUtil from './tool/QuillToolUtil'
import * as tt from './tool/QuillToolType'

export const get = (content, placeholder) => {
  var qI = new QuillEditor();
  qI.InitialContent = content;
  qI.PlaceHolder = placeholder;
  return qI;
}

// Property
export const GetDefault_Tools = (q) => {
  return [
    toolUtil.get(tt.header, q),
    toolUtil.get(tt.bold, q),
    toolUtil.get(tt.italic, q),
    toolUtil.get(tt.underline, q),

    toolUtil.get(tt.align_left,q),
    toolUtil.get(tt.align_middle,q),
    toolUtil.get(tt.align_right,q),

    toolUtil.get(tt.code_block, q),
    toolUtil.get(tt.blockquote, q),
  ]
}

