import { makeObservable, observable, action } from 'mobx'
import { Button, message } from 'antd'
import * as util from './DictVMUtil'
import * as itemUtil from '../base/BaseVMUtil'

import * as commandType from '../../../user/command/base/CommandType'

import * as genUtil from '../../../../../../util/generalUtil'
import * as commandUtil from '../../../../../../util/commandUtil'

export class DictVM {
  constructor() {
    makeObservable(this, {
      items: observable,
      CurrentItem: observable,
    })
  }

  get id() {
    return this.ModelItem.id
  }

  get IsCreator() {
    return this.ModelItem.IsCreator
  }

  // Item
  OnSet_Items_Func = []

  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    }
    return this.items
  }
  set Items(v) {
    this.items = v

    genUtil.Handle(this.OnSet_Items_Func, (v) => (this.OnSet_Items_Func = v))
  }

  AddItem = () => {
    var q = this.ModelItem.AddItem()
    var qI = itemUtil.get(q, this)

    this.Items.push(qI)
    return qI
  }

  CurrentItem = undefined

  // validate
  Validate = (level) => {
    return commandUtil.Validate(undefined, { type: commandType.content, level })
  }

  // view
  // table
  get ItemsView() {
    return this.Items?.map((x) => x.TitleView)
  }

  get View() {
    return (
      <div className="flex">
        {/* Items */}
        <div className="flex">
          {
            // this.IsCreator &&
            true ? (
              <div className="mr-2">
                <Button
                  type="primary"
                  size="small"
                  className="btn-success"
                  onClick={this.AddItem}
                >
                  +
                </Button>
              </div>
            ) : (
              ''
            )
          }

          <div className="flex-1">{this.ItemsView}</div>
        </div>
      </div>
    )
  }
}
