import { makeObservable, observable, action } from 'mobx'
import { PMData } from '../model/PMData'
import * as util from './PMDataVMUtil'
import * as genUtil from '../../../../util/generalUtil'
import { Route, BrowserRouter, HashRouter, Switch } from 'react-router-dom'
import LayoutView from '../../entity/layout/layout/LayoutView'
import PMDataView from './PMDataView'
import * as vt from '../../entity/view/ViewType'
import * as vm from '../../entity/view/ViewMode'

export class PMDataVM {
  constructor() {
    makeObservable(this, {
      isAdmin: observable,
      routeData: observable,
      viewMode: observable,
    })
  }

  static instance
  static get Instance() {
    if (!this.instance) {
      this.instance = util.get()
    }
    return this.instance
  }
  static set Instance(v) {
    this.instance = v
  }

  get pmData() {
    return PMData.Instance
  }

  // route
  routeData = undefined
  get RouteData() {
    if (!this.routeData) {
      this.routeData = this.pmData.RouteData
    }
    return this.routeData
  }
  set RouteData(v) {
    this.routeData = v
    this.pmData.RouteData = v

    if (v[0].type === 'addin') {
      this.AddinGroup.CurrentItem = undefined
    }
  }

  // Layout
  layout
  get Layout() {
    if (!this.layout) {
      this.layout = util.GetLayout(this)
    }
    return this.layout
  }

  OnSet_IsAdmin_Func_Dict = []

  // User
  isAdmin = undefined
  get IsAdmin() {
    return this.isAdmin
  }
  set IsAdmin(v) {
    this.isAdmin = v

    genUtil.Handle(
      this.OnSet_IsAdmin_Func_Dict,
      (v) => (this.OnSet_IsAdmin_Func_Dict = v)
    )
  }

  // Data
  // UserDict
  userDict
  get UserDict() {
    if (!this.userDict) {
      this.userDict = util.GetUserDict(this)
    }
    return this.userDict
  }

  // role
  roleDict
  get RoleDict() {
    if (!this.roleDict) {
      this.roleDict = util.GetRoleDict(this)
    }
    return this.roleDict
  }

  // coin
  coinGroup
  get CoinGroup() {
    if (!this.coinGroup) {
      this.coinGroup = util.GetCoinGroup(this)
    }
    return this.coinGroup
  }

  tradeGroup
  get TradeGroup() {
    if (!this.tradeGroup) {
      this.tradeGroup = util.GetTradeGroup(this)
    }
    return this.tradeGroup
  }

  // View
  viewMode = undefined
  get ViewMode() {
    if (this.viewMode === undefined) {
      this.viewMode = this.pmData.ViewMode
    }
    return this.viewMode
  }
  set ViewMode(v) {
    this.viewMode = v
    this.pmData.ViewMode = v
  }

  get MainView() {
    return <PMDataView store={this} />
  }

  get WrapperView() {
    return this.MainView
  }

  UnAuthorized_View = (
    <span className="text-[#ff4d4f] font-medium text-sm">
      Bn khng c quyn truy cp View hin ti!
    </span>
  )
}
