import { DictVM } from "./DictVM";
import * as iUtil from '../item/ItemVMUtil'

import * as genUtil from '../../../../../util/generalUtil'

// Instance
export const get = (q, dict) => {
  var qI = new DictVM();

  qI.ModelItem = q;
  qI.Dict = dict

  return qI;
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x=> iUtil.get(x, q));
    q.IsGet_Items = false;
  }

  var host = q.ModelItem
  if (host.Items) {
    setObj(host.Items)
  }

  genUtil.Add_2(
    host.OnSet_Items_Func,
    () => setObj(host.Items),
    false
  )
}