import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, message, Switch } from 'antd';
import './Layout.css'
import { Layout, Vi_Comp } from './Layout';
import HeaderView from '../header/HeaderView';
import BodyView from '../body/BodyView';
import FooterView from '../footer/FooterView';
import { withRouter } from "react-router-dom";
import * as vs from '../../view/ViewState'
import { PMDataVM } from '../../../single/form/PMDataVM';
import * as vt from '../../view/ViewType'

const pmDataVM = () => PMDataVM.Instance

class LayoutView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new Layout()
    } else {
      this.state = store;
    }
  }

  componentDidMount() {
    var data = this.state;
    const { type, meta, type2 } = this.props.match.params;
    vt.Handle(type, meta, type2);
  }

  componentDidUpdate() {
    var data = this.state;
    const { type, meta, type2 } = this.props.match.params;
    vt.Handle(type, meta, type2);
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='layout__wrapper'>
          {/* Header */}
          <HeaderView store={data.Header} />

          {/* Body */}
          <BodyView store={data.Body} />

          {/* Footer */}
          {/* <FooterView store={data.Footer} /> */}

          <div className='h-36' />
        </div>
      </>
    );
  }
}

export default withRouter(observer(LayoutView));
