import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Col, Row } from 'antd';
import { PMDataVM } from './PMDataVM';
// import { Route, BrowserRouter as Router, Switch } from 'react-router-dom';
import { Route, HashRouter as Router, Switch } from 'react-router-dom';
import LayoutView from '../../entity/layout/layout/LayoutView';
import './PMData.css'

class PMDataView extends Component {
  render() {
    var data = this.data;

    return (
      <>
        <Router basename={process.env.PUBLIC_URL}>
          <Route path='/:type?/:meta?/:type2?' exact component={LayoutView} />
        </Router>
      </>
    );
  }
}

export default observer(PMDataView);
