import { Dict } from './Dict'
import * as itemUtil from '../base/BaseUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import { PMData } from '../../../../single/model/PMData'

const pmData = () => PMData.Instance

// Instance
export const get = (dict) => {
  var qI = new Dict()

  qI.Dict = dict

  return qI
}

export const GetTest = () => {
  return [
    {
      _id: genUtil.newGUID(),
      code: 'BTC',
      name: 'Bitcoin',
      ref_rationame: 'BTC USDT',
    },
    // { _id: genUtil.newGUID(), name: 'Nim tin' }
  ]
}

// Property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined
  q.IsGet_Items = true

  const setObj = (data) => {
    q.Items = data.map((x) => itemUtil.GetFromData(x, q))
    q.IsGet_Items = false
  }

  // setObj(GetTest())

  // var host = pmData().UserDict

  const getApi = () => {
    var apiData = {}

    itemUtil.GetData(apiData, (data) => {
      setObj(data)
    })
  }

  getApi()

  // if (host.LoginUser) {
  //   getApi()
  // }

  // genUtil.Add_2(host.OnSet_loginId_Func, getApi, false)
}

// Method
export const GetItem = (q, _id, handle) => {
  itemUtil.GetOne(
    {
      _id,
    },
    (data) => {
      data = data ? itemUtil.GetFromData(data) : data
      handle(data)
    }
  )
}
