import { RefDict } from '../model/ref/RefDict';

export const GetRefData = (q) => {
  if (!q.Target_RefData) {
    q.Target_RefData = new RefDict();
  }

  return q.Target_RefData
}

export const GetRefItem = (q, refName) => {
  if (!q.Target_RefData) {
    q.Target_RefData = new RefDict();
  }

  return q.Target_RefData.GetItem(refName);
}

export const CheckOutside = (e, ref) => {
  try {
    return ref && ref.current && !ref.current.contains(e.target)
  } catch {
    console.log(ref.current)
    throw 500;
  }
}

export const ClickOutsite = (q, refName, handle) => {
  var item = GetRefItem(q, refName);

  if (!item.Events) {
    item.Events = []
  }

  var ref = item.value;

  item.Events.push({
    key: 'mousedown',
    func: (e) => {
      if (CheckOutside(e, ref)) {
        handle(e);
      }
    }
  })
}

export const Export_RefData = (q, loadType) => {
  if (q.Target_RefData) {
    q.Target_RefData.Items.forEach(item => {
      if (!item.Events) return;

      item.Events.forEach(x => {
        // mount
        if (loadType === 'mount') {
          document.addEventListener(x.key, x.func);
        }

        // unmount
        if (loadType === 'unmount') {
          document.removeEventListener(x.key, x.func);
        }
      })
    })
  }
}