import { ItemVM } from './ItemVM'
import { EntDelete } from '../../../../../delete/EntDelete';
import { Button, message } from 'antd';

import * as commandUtil from '../../command/base/CommandVMUtil'

import * as inputUtil from '../../../../../../util/inputUtil'

// Instance
export const get = (q, dict) => {
  var qI = new ItemVM();
  qI.ModelItem = q;
  qI.Dict = dict;
  return qI;
}

// Property
export const GetDeleteVM = (q) => {
  var qI = new EntDelete();

  qI.ToggleType = 'manual'
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete();
  }

  return qI;
}

export const GetCommands = (q) => {
  return q.ModelItem.Commands.map(x => commandUtil.get(x, q));
}

// view
export const GetRowView = (q) => {
  return <tr key={q.id}>
    <td
      onMouseEnter={
        () => inputUtil.Edit(q.NameVM)
      }
      onMouseLeave={
        () => inputUtil.EditOK(q.NameVM)
      }
    >{q.NameView}</td>

    {/* Command */}
    {q.Commands.map(x => x.View)}

    {/* Delete */}
    <td className='non-input text-center'>
      {q.DeleteVM.View}
    </td>
  </tr>
}