import {Item} from './Item'
import * as commandUtil from '../../command/base/CommandUtil'
import * as commandType from '../../command/base/CommandType'
import {url} from './Item_Url'

import * as api from '../../../../../../util/apiUtil'

// instance
export const GetFromData = (data, dict) => {
  var qI = get(dict)

  qI._id = data._id
  qI.Name = data.name
  
  // map command
  data.commands.forEach(x => {
    qI.GetCommand(x.type).Level = x.level
  })

  return qI;  
}

export const get = (dict) => {
  var qI = new Item();
  qI.Dict = dict;
  return qI;
}

// Api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}

// property
export const GetCommands = (q) => {
  return commandType.List().map(x => {
    var qI = commandUtil.get(q);
    qI.Type = x;
    return qI;
  })
}