import { CheckCircleOutlined, LinkOutlined } from '@ant-design/icons'
import { Link } from 'react-router-dom'
import { BaseVM } from './BaseVM'
import { EntDelete } from '../../../../../delete/EntDelete'
import * as f from '../field/Field'
import * as fieldUtil from '../../../../../field/FieldUtil'
import * as modelUtil from './BaseUtil'

import * as inputUtil from '../../../../../../util/inputUtil'
import * as genUtil from '../../../../../../util/generalUtil'

import { PMDataVM } from '../../../../single/form/PMDataVM'
import { Switch } from 'antd'

const pmDataVM = () => PMDataVM.Instance

// Instance
export const get = (q, dict) => {
  var qI = new BaseVM()
  qI.ModelItem = q
  qI.Dict = dict
  return qI
}

// Property
// field
export const GetFields = (q) => {
  var inputs = f.List()

  var host = q.ModelItem
  var fields = []

  inputs.forEach((x, i) => {
    var fName = f.GetName(x)

    var qI
    qI = fieldUtil.get(
      () => host[fName],
      (v) => {
        host[fName] = v
      },
      q.Save
    )

    qI.OnSetValue = (v) => {
      if (qI.IsStopPropagation) {
        qI.IsStopPropagation = false
        return
      }

      switch (x) {
        case f.initialprice:
        case f.takeprofitprice:
        case f.takeprofitprecent:
        case f.stoplossprice:
        case f.stoplosspercent:
          q.Is_SaveCancel = true

          var targetFields
          switch (x) {
            case f.initialprice:
              targetFields = [f.takeprofitprice, f.stoplossprice]
              break
            case f.takeprofitprice:
              targetFields = [f.takeprofitprecent]
              break
            case f.takeprofitprecent:
              targetFields = [f.takeprofitprice]
              break
            case f.stoplossprice:
              targetFields = [f.stoplosspercent]
              break
            case f.stoplosspercent:
              targetFields = [f.stoplossprice]
              break
          }

          targetFields.forEach((field) => Refresh_Field(q, field))

          q.Is_SaveCancel = false
          break
      }
    }

    // qI.PreReady = () => q.Validate('edit') && pmDataVM().ViewMode === 'edit'
    // qI.PreEdit = () => q.Validate('edit')

    var validate = () => !q.IsSubmit
    qI.PreReady = () => {
      return validate()
      // && pmDataVM().ViewMode === 'edit'
    }
    qI.PreEdit = validate

    qI.Key = x
    fields.push(qI)
  })

  return fields
}

export const GetDeleteVM = (q) => {
  var qI = new EntDelete()

  qI.ToggleType = 'manual'
  qI.Title = 'Xc nhn xa?'

  qI.OnRun = () => {
    q.Delete()
  }

  return qI
}

export const Refresh_Field = (q, field) => {
  var val
  var model = q.ModelItem

  switch (field) {
    case f.takeprofitprice:
      val = modelUtil.GetTakeProfit_Price(model)
      break
    case f.stoplossprice:
      val = modelUtil.GetStopLoss_Price(model)
      break
    case f.takeprofitprecent:
      val = modelUtil.GetTakeProfit_Percent(model)
      break
    case f.stoplosspercent:
      val = modelUtil.GetStopLoss_Percent(model)
      break
  }

  q.SetField(field, val)

  // q.Is_SaveCancel = true

  // var model = q.ModelItem

  // q.SetField(f.stoplossprice, modelUtil.GetStopLoss_Price(model))

  // q.Is_SaveCancel = false
  // q.Save()
}

// view
export const GetRowView = (q) => {
  var model = q.ModelItem

  return (
    <tr key={q.id}>
      <td className="non-input">{q.Index + 1}</td>
      <td className="non-input">{!q.IsSubmit ? q.DeleteVM.View : ''}</td>

      {!q.InCoin ? (
        <td className="non-input font-medium">{model.Coin?.Code}</td>
      ) : undefined}

      <td className="non-input">
        <div className="flex">
          <div className={`${q.Type === 'long' ? '' : 'bg-[#ff4d4f]'}`}>
            <Switch
              checkedChildren="long"
              unCheckedChildren="short"
              checked={q.Type === 'long'}
              onChange={(v) => (q.Type = v ? 'long' : 'short')}
              disabled={q.IsSubmit}
            />
          </div>
        </div>
      </td>
      <td className="font-medium">{q.GetField(f.initialprice).WrapperView}</td>
      <td>
        <div className="flex items-center">
          <div
            className={`ml-2 mr-1 cursor-pointer text-base text-[#1890ff]
              ${!q.IsSubmit ? '' : 'invisible'}`}
            onClick={() => q.Submit('profit')}
          >
            <CheckCircleOutlined />
          </div>
          <div>
            <div
              className={`font-medium ${
                q.Type === 'long' ? 'text-[#1890ff]' : 'text-[#ff4d4f]'
              }`}
            >
              {q.GetField(f.takeprofitprice).WrapperView}
            </div>
            <div className={`pt-1`}>
              {q.GetField(f.takeprofitprecent).WrapperView}
            </div>
          </div>
        </div>
      </td>
      <td>
        <div className="flex items-center">
          <div
            className={`ml-2 mr-1 cursor-pointer text-base text-[#ff4d4f]
            ${!q.IsSubmit ? '' : 'invisible'}`}
            onClick={() => q.Submit('loss')}
          >
            <CheckCircleOutlined />
          </div>
          <div>
            <div
              className={`font-medium ${
                q.Type === 'short' ? 'text-[#1890ff]' : 'text-[#ff4d4f]'
              }`}
            >
              {q.GetField(f.stoplossprice).WrapperView}
            </div>
            <div className="pt-1">
              {q.GetField(f.stoplosspercent).WrapperView}
            </div>
          </div>
        </div>
      </td>

      <td className="non-input">
        <div>
          {/* Risk Reward */}
          <div className="flex font-medium">
            <span className="text-[#ff4d4f]">1</span>
            <span className="mx-2">:</span>
            <span className="text-[#1890ff]">
              {model.RewardRisk_Ratio.toFixed(1)}
            </span>
          </div>

          {/* Earn */}
          {q.IsSubmit ? (
            <div
              className={`font-medium mt-2
            ${model.EarnPercent > 0 ? 'text-[#1890ff]' : 'text-[#ff4d4f]'}`}
            >
              {model.EarnPercent}
            </div>
          ) : (
            ''
          )}
        </div>
      </td>
    </tr>
  )
}
