import { PMDataVM } from '../../single/form/PMDataVM'
import * as genUtil from '../../../../util/generalUtil'

export const home = 'home'
export const role = 'role'
export const coin = 'coin'
export const trade = 'trade'

const pmDataVM = () => PMDataVM.instance

export const Handle = (type, meta, type2) => {
  type = type ? type : home

  var qI = pmDataVM()
  qI.RouteData = [
    {
      type,
      meta,
    },
    {
      type: type2,
    },
  ]
}
