import { CommandVM } from './CommandVM'
import { Button, message } from 'antd';
import * as commandLevel from './CommandLevel'
import './Command.css'

// Instance
export const get = (q, dict) => {
  var qI = new CommandVM();
  qI.ModelItem = q;
  qI.Dict = dict;
  return qI;
}

// Property
export const GetView = (q) => {
  return <td className='text-center non-input' key={q.id}>
    <div className='f-jc'>
      <div onClick={q.UpLevel}
        className={`role__command ${q.Level} f-jc`}
      >
        {q.Level}
      </div>
    </div>
  </td>
}

export const UpLevel = (q) => {
  var list = commandLevel.List();
  var index = list.indexOf(q.Level)

  var n_index = index !== list.length - 1 ? index + 1 : 0
  q.Level = list[n_index];
}