import { makeObservable, observable, action } from 'mobx'
import * as util from './GroupVMUtil'

import * as genUtil from '../../../../../util/generalUtil'

export class GroupVM {
  constructor() {
    makeObservable(this, {})
  }

  futureDict
  get FutureDict() {
    if (!this.futureDict) {
      this.futureDict = util.GetFutureDict(this)
    }
    return this.futureDict
  }

  // view
  get View() {
    return util.GetView(this)
  }
}
