import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Select } from "antd";
import { SelectVM } from "./SelectVM";
import * as floatItemVMUtil from '../float/FloatItemVMUtil'
import FloatItemView from "../float/FloatItemView";
import * as genUtil from '../../util/generalUtil'
import './Select.css'

const { Option } = Select;

class SelectView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new SelectVM();
    } else {
      this.state = store;
    }
  }

  onValueChange = (v) => {
    var data = this.state;
    data.SelectedIndex = v;
  }

  onDropdownVisibleChange = (v) => {
    var data = this.state;
    data.Open = v;
  }

  render() {
    var data = this.state;
    var className = this.props.className ? this.props.className : ''
    var style = this.props.style ? this.props.style : { width: '100%' };
    var { Placeholder, Items, SelectedIndex, InitialIndex } = data;

    if (SelectedIndex === -1) {
      SelectedIndex = InitialIndex;
      data.InitialIndex = -1;
    }

    // debugger
    var elems = Items ? Items.map((item, index) => {
      var displayContent = data.TitleRender(item);

      var id = undefined;
      if (data.IdRender) {
        id = data.IdRender(item)
      } else {
        if (item === undefined) {
          id = genUtil.randomId();
        } else if (typeof item === 'string' || !isNaN(item)) {
          id = item;
        } else {
          id = item._id ? item._id : item.id;
        }
      }

      return <Option key={id} value={index}>{displayContent}</Option>
    }) : "";

    var floatElem = ''
    if (data.CanClear) {
      var floatItemStore = floatItemVMUtil.get('right')
      floatItemStore.wrapper = <Button size='small' type='primary'
        onClick={data.Clear}>x</Button>

      floatElem = <FloatItemView store={floatItemStore} />;
    }

    return (
      <>
        <div 
          className={`select__wrapper ${className}`}
          style={data.DivStyle} >
          {floatElem}
          <Select
            className='select__main'
            style={style}
            showSearch
            placeholder={Placeholder}
            optionFilterProp="children"
            onChange={this.onValueChange}
            value={SelectedIndex}
            filterOption={(input, option) =>
              option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }
            open={data.CanOpen && data.Open}
            onDropdownVisibleChange={this.onDropdownVisibleChange}
            disabled={data.Disabled || data.IsDisabled}
            onFocus={data.OnFocus}

          >
            {elems}
          </Select>
        </div>
      </>
    )
  }
}

export default observer(SelectView)