import { User } from "./User"
import * as api from '../../../../util/apiUtil'
import * as commandDictUtil from '../command/base/DictUtil'

// Instance
export const GetFromDb = (data, dict) => {
  var qI = get(dict);
  qI._id = data._id
  qI.Username = data.username;
  qI.Name = data.name;

  if (data.userToken){
    dict.Token = data.userToken
  }
  
  return qI;
}

export const get = (dict) => {
  var qI = new User();
  qI.Dict = dict;
  return qI;
}

// property
export const GetCommandDict = (q) => {
  return commandDictUtil.get(q);
}

// Api
export const GetData = (url, apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData);

  apiFunc().then(response => response.data)
    .then(data => {
      if (handle) {
        handle(data)
      }
    })
}