import { message } from 'antd';
import * as genUtil from '../../../../../util/generalUtil'
import * as util from './LayoutUtil'
import { makeObservable, observable, action } from 'mobx';
import * as vs from '../../view/ViewState'

export class Layout {
  constructor() {
    makeObservable(this, {
      
    })
  }

  _id

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  header
  get Header() {
    if (!this.header) {
      this.header = util.GetHeader(this);
    }
    return this.header;
  }

  body
  get Body() {
    if (!this.body) {
      this.body = util.GetBody(this);
    }
    return this.body;
  }

  footer
  get Footer() {
    if (!this.footer) {
      this.footer = util.GetFooter(this);
    }
    return this.footer;
  }
}