import * as itemUtil from '../item/ItemUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import { Dict } from './Dict'

// instance
export const get = (dict) => {
  var qI = new Dict();

  qI.Dict = dict;

  return qI;
}

// property
export const Handle_Items = (q) => {
  if (q.IsGet_Items) return undefined;
  q.IsGet_Items = true;

  const setObj = (data) => {
    q.Items = data.map(x => itemUtil.GetFromData(x, q))
    q.IsGet_Items = false;
  }

  // setObj([]);

  const getApi = () => {
    var apiData = {
    }

    itemUtil.GetData(apiData, (data) => {
      setObj(data)
    })
  }

  getApi()
}

// method
export const GetItem = (q, name, handle) => {
  if (!name){
    handle(name)
  }

  const getObj = () => {
    var list = q.Items;
    var item = genUtil.SingleFilter(list, x => x.Name === name);

    handle(item);
  }

  if (q.Items) {
    getObj();
  } else {
    genUtil.Add_2(q.OnSet_Items_Func,
      getObj);
  }
}