import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { QuillTool } from './QuillTool';
import './QuillTool.css'

class QuillToolView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new QuillTool()
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        {data.ToolView}
      </>
    );
  }
}

export default observer(QuillToolView);
