import { url } from './Base_Url'
import { Base } from './Base'

import * as api from '../../../../../../../util/apiUtil'

// Instance
export const GetFromData = (data, dict) => {
  var qI = get(dict)

  qI._id = data._id
  qI.Description = data.description
  qI.addinId = data.addinId

  return qI
}

export const get = (dict) => {
  var qI = new Base()

  qI.Dict = dict

  return qI
}

export const Clone = (q, dict) => {
  var qI = get(dict)

  qI._id = q._id
  qI.Name = q.Name

  return qI
}

// Property

// api
export const GetData = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/list`, apiData)

  apiFunc()
    .then((response) => response.data)
    .then((data) => {
      if (handle) {
        handle(data)
      }
    })
}

export const GetOne = (apiData, handle) => {
  var apiData = apiData ? apiData : {}
  const apiFunc = () => api.post(`${url}/get`, apiData)

  apiFunc()
    .then((response) => response.data)
    .then((data) => {
      if (handle) {
        handle(data)
      }
    })
}
