import { makeObservable, observable, action } from 'mobx';
import * as util from './DictVMUtil'
import TableView from '../table/TableView';
import * as itemUtil from '../item/ItemVMUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import './Dict.css'
import { Button } from 'antd';

export class DictVM {
  constructor() {
    makeObservable(this, {
      items: observable
    })
  }

  get id() {
    return this.ModelItem.id;
  }

  // Item
  OnSet_Items_Func = []

  items = undefined
  get Items() {
    if (!this.items) {
      util.Handle_Items(this)
    } return this.items
  } set Items(v) {
    this.items = v;

    genUtil.Handle(this.OnSet_Items_Func,
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = () => {
    var q = this.ModelItem.AddItem();
    var qI = itemUtil.get(q, this);

    this.Items.push(qI)
    return qI;
  }

  // table
  get TableView() {
    return <TableView store={this} />
  }

  get View() {
    return <div className='mt__wrapper'>
      <div className='f-jc text-lg font-medium mb-3'>
        <span>Phn quyn</span>
      </div>

      <div className='f'>
        <Button type='primary' size='small'
          className='btn-success mt__add'
          onClick={this.AddItem}>+</Button>

        <div className='f1'>
          <TableView store={this} />
        </div>
      </div>
    </div>
  }
}