import * as util from './DictUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import * as itemUtil from '../item/ItemUtil'

export class Dict {
  // item
  OnSet_Items_Func = []

  items
  get Items(){
    if (!this.items){
      util.Handle_Items(this)
    } return this.items; 
  } set Items(v){
    this.items = v;

    genUtil.Handle(this.OnSet_Items_Func, 
      (v) => this.OnSet_Items_Func = v)
  }

  AddItem = () => {
    var qI = itemUtil.get(this);

    this.Items.push(qI)
    return qI;
  }

  GetItem = (name, handle) => {
    util.GetItem(this, name, handle)
  }
}