import { makeObservable, observable, action } from 'mobx';

import * as util from './TargetUtil'
import * as genUtil from '../../../../../../../util/generalUtil'

export class Target {
  constructor() {
    makeObservable(this, {
      role: observable,
    })
  }

  Dict

  Value

  Type

  IsEqual = (other) => {
    return util.IsEqual(this, other)
  }

  get QueryData() {
    return util.GetQueryData(this)
  }

  roleUrl
  get RoleUrl() {
    if (!this.roleUrl) {
      this.roleUrl = util.GetRoleUrl(this)
    } return this.roleUrl
  }

  // role
  OnSet_Role_Func = []

  role = undefined
  get Role() {
    if (this.role === undefined) {
      util.Handle_Role(this)
    } return this.role
  } set Role(v) {
    this.role = v;

    genUtil.Handle(this.OnSet_Role_Func,
      (v) => this.OnSet_Role_Func = v)
  }

  Refresh_Role = () => {
    util.Handle_Role(this)
  }

  get Commands() {
    return this.Role?.Commands;
  }

  Validate = (commandData) => {
    return util.Validate(this, commandData)
  }
}