import { makeObservable, observable, action } from 'mobx'
import { Button, message } from 'antd'
import * as util from './BaseVMUtil'
import * as f from '../field/Field'

import * as genUtil from '../../../../../../util/generalUtil'

export class BaseVM {
  constructor() {
    makeObservable(this, {
      type: observable,
    })
  }

  get id() {
    return this.ModelItem.id
  }

  // data
  fields
  get Fields() {
    if (!this.fields) {
      this.fields = util.GetFields(this)
    }
    return this.fields
  }

  GetField = (name) => {
    return genUtil.SingleFilter(this.Fields, (x) => x.Key === name)
  }

  SetField = (name, val) => {
    var field = this.GetField(name)
    field.IsStopPropagation = true

    var valVM = field.ValueVM
    valVM.RenderState = 'edit'
    valVM.Content = val

    valVM.EditOK()
  }

  Refresh_Field = (name) => {
    util.Refresh_Field(this, name)
  }

  type = undefined
  get Type() {
    if (!this.type) {
      this.type = this.ModelItem.Type
    }
    return this.type
  }
  set Type(v) {
    this.type = v
    this.ModelItem.Type = v

    this.Is_SaveCancel = true

    var list = [
      f.takeprofitprice, //
      f.stoplossprice,
    ]

    list.forEach((x) => {
      this.Refresh_Field(x)
    })

    this.Is_SaveCancel = false
    this.Save()
  }

  get IsSubmit() {
    return this.ModelItem.IsSubmit
  }

  Submit = (type) => {
    this.ModelItem.Submit(type)
  }

  get InCoin() {
    return this.Dict.InCoin
  }

  get IsOwned() {
    return this.ModelItem.IsOwned
  }

  // creator
  get IsCreator() {
    return this.Dict.IsCreator
  }

  get IsOwner() {
    return this.IsCreator || this.ModelItem.IsOwner
  }

  // css
  get Css() {
    return this === this.Dict.CurrentItem
      ? 'bg-[#52c41a90] text-white font-medium'
      : ''
  }

  // current
  SetCurrent = () => {
    var dict = this.Dict

    if (dict.CurrentItem === this) {
      dict.CurrentItem = undefined
    } else {
      dict.CurrentItem = this
    }
  }

  // save
  Save = (handle) => {
    const finish = () => {
      if (handle) {
        handle()
      }
    }

    if (this.Is_SaveCancel) {
      finish()
      return
    }

    this.ModelItem.Save(finish)
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.ModelItem.Delete()
  }

  deleteVM
  get DeleteVM() {
    if (!this.deleteVM) {
      this.deleteVM = util.GetDeleteVM(this)
    }
    return this.deleteVM
  }

  // validate
  Validate = (level) => {
    return this.Dict.Validate(level)
  }

  // view
  get RowView() {
    return util.GetRowView(this)
  }
}
