import * as renderUtil from './TextRenderItemUtil'

export default class TextRender_Dict {
  Items = []

  GetRender = (state) => {
    var list = this.Items;
    var index = list.findIndex(x => x.State === state);
    if (index === -1){
      var item = renderUtil.get(state);
      list.push(item);
      return item;
    }
    return list[index];
  }
}