import { makeObservable, observable, action } from 'mobx';
import * as genUtil from '../../util/generalUtil'

export class Image {
  constructor() {
    makeObservable(this, {
      Source: observable,
      Alt: observable,
      Width : observable,
      Height: observable
    })
  }

  _id

  tempId
  get TempId(){
    if (!this.tempId){
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }


  Source = undefined

  Alt = undefined

  Width = undefined

  Height = undefined
}