import { makeObservable, observable, action } from 'mobx';
import * as util from './ItemVMUtil'
import * as inputUtil from '../../../../../util/inputUtil'
import EditableTextView from '../../../../text/EditableTextView';
import { message } from 'antd';

export class ItemVM {
  constructor() {
    makeObservable(this, {
      tempUsername: observable,
      IsPasswordChanged: observable
    })
  }

  User

  // Username
  get Username() {
    return this.User.Username
  } set Username(v) {
    this.User.Username = v;
  }

  tempUsername = undefined
  get TempUsername() {
    if (!this.tempUsername) {
      this.tempUsername = this.Username;
    } return this.tempUsername
  } set TempUsername(v) {
    this.tempUsername = v;
  }

  usernameVM
  get UsernameVM() {
    if (!this.usernameVM) {
      this.usernameVM = inputUtil.GetInput(this, 'TempUsername')
    } return this.usernameVM
  }

  get UsernameView() {
    return <EditableTextView
      store={this.UsernameVM} />
  }

  // Password
  get Password() {
    return this.User.Password
  } set Password(v) {
    this.User.Password = v;
  }

  passwordVM
  get PasswordVM() {
    if (!this.passwordVM) {
      var qI = this.passwordVM =
        inputUtil.GetInput(this, 'TempPassword', () => {
          this.Password = this.TempPassword;
          this.TempPassword = qI.Content = ''
          this.IsPasswordChanged = true;
        })
      qI.Placeholder = 'password ...'
    } return this.passwordVM
  }

  get PasswordView() {
    return <EditableTextView
      store={this.PasswordVM} />
  }

  // Changed
  get IsUsernameChanged() {
    return this.Username !== this.TempUsername
  }

  IsPasswordChanged = false

  get IsChanged() {
    return this.IsUsernameChanged || this.IsPasswordChanged
  }

  // Row
  get RowView() {
    return util.GetRowView(this)
  }

  // Command
  OK = () => {
    this.Username = this.TempUsername
    this.IsPasswordChanged = false

    this.User.Save(() => {
      message.success('save successfully!');
    });
  }

  Cancel = () => {
    this.UsernameVM.Content = this.TempUsername = this.Username
    this.IsPasswordChanged = false
  }

  // Index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  // Delete
  deleteVM
  get DeleteVM(){
    if (!this.deleteVM){
      this.deleteVM = util.GetDeleteVM(this)
    } return this.deleteVM;
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)

    this.User.Delete();
  }
}