import * as util from './ItemUtil'
import * as genUtil from '../../../../../../util/generalUtil'
import {url} from './Item_Url'
import { message } from 'antd'

export class Item {
  OnSet_id_Func_Dict = []
  __id
  get _id() {
    return this.__id
  } set _id(v) {
    this.__id = v;
    genUtil.Handle(this.OnSet_id_Func_Dict,
      (v) => this.OnSet_id_Func_Dict = v)
  }

  tempId
  get TempId() {
    if (!this.tempId) {
      this.tempId = genUtil.newGUID();
    }
    return this.tempId;
  }

  get id() {
    return this._id ? this._id : this.TempId;
  }

  Name = 'tn vai tr'

  // command
  commands
  get Commands(){
    if (!this.commands){
      this.commands = util.GetCommands(this)
    } return this.commands;
  }

  GetCommand = (type) => {
    return genUtil.SingleFilter(this.Commands, x => x.Type === type);
  }

  // apidata
  get ApiData(){
    return {
      _id : this._id,
      name : this.Name,
      commands : this.Commands.filter(x => x.Level !== 'none')
        .map(x => x.ApiData)
    }
  }

  // Save
  Save = (handle) => {
    this.SaveDatabase(false, handle)
  }

  SaveDatabase = (showMessage = true, handle) => {
    genUtil.SaveDatabase(this, url,
      () => {
        if (showMessage) {
          message.success('save successfully!')
        }
        if (handle) {
          handle();
        }
      });
  }

  // index
  get CurrentList() {
    return this.Dict.Items
  }

  get Index() {
    return this.CurrentList.indexOf(this)
  }

  Delete = () => {
    this.CurrentList.splice(this.Index, 1)
    this.DeleteDatabase(false)
  }

  DeleteDatabase = (showMessage = true, handle) => {
    genUtil.DeleteDatabase(this, url, () => {
      if (showMessage){
        message.success('delete successfully!');
      }
      if (handle){
        handle()
      }
    });
  }
}