export const GetTakeProfit_Price = (type, price, percent) => {
  var val = price * (1 + (type === 'long' ? 1 : -1) * percent)

  var pow = val < 10 ? 3 : val < 100 ? 2 : val < 1000 ? 1 : 0
  var mul = Math.pow(10, pow)

  return Math.ceil(val * mul) / mul
}

export const GetTakeProfit_Percent = (type, price, profit) => {
  var val = ((profit - price) * (type === 'long' ? 1 : -1)) / price
  return Math.ceil(val * 10000) / 10000
}

export const GetStopLoss_Price = (type, price, percent) => {
  var val = price * (1 + (type === 'long' ? -1 : 1) * percent)
  return Math.ceil(val * 10) / 10
}

export const GetStopLoss_Percent = (type, price, loss) => {
  var val = ((loss - price) * (type === 'long' ? -1 : 1)) / price

  var pow = val < 10 ? 3 : val < 100 ? 2 : val < 1000 ? 1 : 0
  var mul = Math.pow(10, pow)

  return Math.ceil(val * mul) / mul
}
